import { useSelector } from 'app/state'
import { Placeholder } from 'components/ChatMessage'
import { Focus } from 'features/Chat/slice'
import React from 'react'
import selectors from 'selectors'
import ChatMessageItem from './ChatMessageItem'
import ReturnedReminderItem from './ReturnedReminderItem'

interface Props {
  itemId: string
  channelId: string
  previousItemId?: string
  nextItemId?: string
  selected?: Focus
  shownInThread?: boolean
  setFocusToInput: () => void
  onClick: () => void
}

export default function ChatItem(props: Props) {
  const [item] = useSelector((state) => [
    selectors.chatItems.getById(state, props.itemId),
  ])

  if (!item) return <Placeholder />

  if (item.reminder) {
    return (
      <ReturnedReminderItem
        id={props.itemId}
        channelId={props.channelId}
        selected={props.selected}
        onClick={props.onClick}
      />
    )
  }

  return (
    <ChatMessageItem
      id={item.id}
      channelId={props.channelId}
      setFocusToInput={props.setFocusToInput}
      shownInThread={props.shownInThread}
      selected={props.selected}
      onClick={props.onClick}
      previousMessageId={props.previousItemId}
      nextMessageId={props.nextItemId}
    />
  )
}
