import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import * as firebase from '../firebase'
import * as entities from './entities'
import { AppDispatch, RootState } from './store'

export enum Loading {
  Idle,
  Pending,
  Succeeded,
  Failed,
}

export const name = 'session'

interface State {
  orgId: 'superhuman'
  id?: string
  token?: string
  loading: Loading
}

export const initialState: State = {
  orgId: 'superhuman',
  loading: Loading.Idle,
}

export const slice = createSlice({
  name,
  initialState,
  reducers: {
    setSession: (
      state,
      action: PayloadAction<{ id: string; token?: string }>
    ) => {
      state.id = action.payload.id
      state.token = action.payload.token
      state.loading = Loading.Succeeded
    },
  },
})

export const { setSession } = slice.actions
export default slice.reducer

export const initiateSession = function () {
  return async (dispatch: AppDispatch, getState: () => RootState) => {
    const session = await firebase.login()
    if (!session || !session.user.email) return

    const orgId = getState().session.orgId
    const id = await userIdByEmail(session.user.email)

    dispatch(setSession({ id, token: session.token }))
    dispatch(
      entities.set({
        id,
        table: entities.Users,
        record: { id, name: session.user.displayName || '' },
      })
    )

    firebase.set(profilePath(orgId, id), {
      id,
      name: session.user.displayName || '',
      email: session.user.email,
      photoUrl: session.user.photoURL,
    })
  }
}

export const syncUserProfiles = function (userIds: string[]) {
  return async (dispatch: AppDispatch, getState: () => RootState) => {
    if (userIds.length === 0) return

    const state = getState()
    const missingIds = userIds.filter((id) => id && !state.entities.users[id])

    if (missingIds.length === 0) return

    const snapshot = await firebase.fetch({
      path: '/orgs/' + state.session.orgId + '/profiles',
      where: [{ field: 'id', op: 'in', value: missingIds }],
    })

    dispatch(
      entities.setBatch(
        snapshot.docs.map((doc) => {
          return {
            id: doc.id,
            table: entities.Users,
            record: { id: doc.id, name: doc.data().name },
          }
        })
      )
    )
  }
}

function profilePath(orgId: string, userId: string): string {
  return ['orgs', orgId, 'profiles', userId].join('/')
}

async function userIdByEmail(email: string): Promise<string> {
  const buffer = new TextEncoder().encode(email)
  const hashBuffer = await crypto.subtle.digest('SHA-256', buffer)
  const hashArray = Array.from(new Uint8Array(hashBuffer))
  const hashHex = hashArray.map((b) => b.toString(16).padStart(2, '0')).join('')
  return hashHex
}
