import { useEffect } from 'react'
import useCommandPalette, {
  CommandRelevancyResult,
} from './use-command-palette'

type UseCommandRegistryCallback = (
  registerCommand: (
    commandName: string,
    commandFn: () => void,
    releancy?: CommandRelevancyResult[]
  ) => void
) => void

export default function useCommandRegistry(
  callbackFn: UseCommandRegistryCallback,
  deps: React.DependencyList
) {
  const registered: string[] = []
  const commandPalette = useCommandPalette()

  useEffect(() => {
    callbackFn(registerCommand)

    return () => {
      commandPalette.deregister(...registered.splice(0, registered.length))
    }

    function registerCommand(
      commandName: string,
      commandFn: () => void,
      relevancy?: CommandRelevancyResult[]
    ) {
      commandPalette.register(commandName, commandFn, relevancy)
      registered.push(commandName)
    }
  }, [...deps])
}
