import TextField from 'components/TextField'
import Timestamp from 'components/Timestamp'
import datetime from 'lib/datetime'
import useFocus from 'lib/use-focus'
import React, { useEffect, useRef } from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import { Draft } from './slice'

interface Props {
  enabled: boolean
  draft: Draft
  placeholder: string
  clearDraft: () => void
  setDraftValue: (value: string) => void
  editLastMessage: () => void
  removeDraftReminder: () => void
  postDraftMessage: () => void
  setFocusToInput: () => void
  selectLastMessage: () => void
}

export default function Input(props: Props) {
  const [textFieldEl, setTextareaFocused] = useFocus<HTMLTextAreaElement>()

  const focusShouldBeRestored = useRef(false)
  const focusTimer = useRef<any>(null)

  useEffect(() => {
    if (props.enabled && textFieldEl) {
      setTextareaFocused()
    } else {
      setTextareaFocused(false)
    }

    focusShouldBeRestored.current = props.enabled
    if (!props.enabled && focusTimer.current !== null) {
      clearTimeout(focusTimer.current)
      focusTimer.current = null
    }
  }, [props.enabled, textFieldEl, setTextareaFocused])

  useHotkeys('enter', onPressEnter, {
    enableOnTags: ['TEXTAREA'],
    keydown: true,
    enabled: props.enabled,
  })

  useHotkeys('up', props.editLastMessage, {
    enableOnTags: ['TEXTAREA'],
    keydown: true,
    enabled: props.enabled,
  })

  useHotkeys('esc', onPressEsc, {
    enableOnTags: ['TEXTAREA'],
    keydown: true,
    enabled: props.enabled,
  })

  useHotkeys('cmd+up', props.selectLastMessage, {
    enableOnTags: ['TEXTAREA'],
    keydown: true,
    enabled: props.enabled,
  })

  return (
    <div className={'compose ' + (props.enabled ? 'writing' : '')}>
      <div className="compose-inner">
        <TextField
          inputRef={textFieldEl}
          placeholder={props.placeholder}
          value={props.draft.body}
          onFocus={onFocus}
          onBlur={onBlur}
          onInput={props.setDraftValue}
        />
      </div>
      {props.draft.remindAt ? (
        <div className="draft-reminder">
          Remind me:{' '}
          <Timestamp epoch={datetime.toEpoch(props.draft.remindAt)} />
        </div>
      ) : null}
    </div>
  )

  function onFocus() {
    if (!props.enabled) {
      focusShouldBeRestored.current = true
      props.setFocusToInput()
    }
  }

  function onBlur() {
    restoreFocus()
  }

  function onPressEnter(e: KeyboardEvent) {
    e.preventDefault()

    if (props.draft.body.trim().length === 0) return

    props.postDraftMessage()
    props.clearDraft()
  }

  function onPressEsc(e: KeyboardEvent) {
    e.preventDefault()
    props.removeDraftReminder()
  }

  function restoreFocus() {
    if (!focusShouldBeRestored.current) {
      return
    }

    focusTimer.current = setTimeout(() => {
      setTextareaFocused()
    }, 500)
  }
}
