import { RootState } from 'app/state'
import selectors from 'selectors'
import { Region } from './slice'

export function isOnCommandPalette(state: RootState): boolean {
  return selectors.commandPalette.isOpen(state)
}

export function isOnChat(state: RootState): boolean {
  return !isOnCommandPalette(state) && state.focusRegion.current === Region.Chat
}

export function isOnChannelSidebar(state: RootState): boolean {
  return (
    !isOnCommandPalette(state) &&
    state.focusRegion.current === Region.ChannelSidebar
  )
}
