import { useDispatch, useSelector } from 'app/state'
import React from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import selectors from 'selectors'
import Chat from '../Chat'
import Discussion from '../Discussion'
import ChannelSidebar from './ChannelSidebar'
import { openChat, openDiscussion, toggleDiscussionView } from './slice'

interface Props {
  id: string
}

export default function Channel(props: Props) {
  const dispatch = useDispatch()
  const [viewingDiscussion, focusNotOnSidebar] = useSelector((state) => [
    selectors.channels.isFocusOnDiscussionTab(state, props.id),
    !selectors.focusRegion.isOnChannelSidebar(state),
  ])

  useHotkeys('tab', onHitTab, {
    enableOnTags: ['TEXTAREA'],
  })

  return (
    <>
      <div className="channel">
        <div className="channel-com-switcher">
          <div className="channel-title">#{props.id}</div>
          <div
            className={`channel-chat-btn ${!viewingDiscussion ? 'on' : ''}`}
            onClick={() => dispatch(openChat(props.id))}
          >
            Chat
          </div>
          <div
            className={`channel-discussions-btn ${
              viewingDiscussion ? 'on' : ''
            }`}
            onClick={() => dispatch(openDiscussion(props.id))}
          >
            Discussions<label>{4}</label>
          </div>
        </div>
        {viewingDiscussion ? (
          <Discussion channelId={props.id} />
        ) : (
          <Chat channelId={props.id} />
        )}
      </div>
      <ChannelSidebar channelId={props.id} />
    </>
  )

  function onHitTab(e: KeyboardEvent) {
    e.preventDefault()

    if (focusNotOnSidebar) {
      dispatch(toggleDiscussionView(props.id))
    }
  }
}
