import * as firebase from 'app/firebase'

interface Subscription {
  unsubscribe?: firebase.Unsubscribe
}

const channelMap: { [channelId: string]: Subscription } = {}

export function attachListener(
  channelId: string,
  unsubscribeFn: firebase.Unsubscribe
) {
  detachListener(channelId)
  channel(channelId).unsubscribe = unsubscribeFn
}

export function detachListener(channelId: string) {
  const unsubscribe = channel(channelId).unsubscribe
  if (unsubscribe) unsubscribe()
}

function channel(channelId: string): Subscription {
  if (channelMap[channelId]) return channelMap[channelId]
  return (channelMap[channelId] = {})
}
