import * as fbapp from 'firebase/app'
import * as fbauth from 'firebase/auth'
import {
  addDoc,
  collection,
  doc,
  DocumentData,
  DocumentReference,
  DocumentSnapshot,
  endAt,
  FieldPath,
  getDoc,
  getDocs,
  getFirestore,
  limit,
  onSnapshot,
  orderBy,
  Query,
  query,
  QueryDocumentSnapshot,
  QuerySnapshot,
  setDoc,
  startAfter,
  where,
  WhereFilterOp,
} from 'firebase/firestore'
import { RootState } from './state'
export { documentId } from 'firebase/firestore'
export type {
  DocumentData,
  QueryDocumentSnapshot,
  Unsubscribe,
} from 'firebase/firestore'

const app = fbapp.initializeApp({
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  databaseURL: process.env.REACT_APP_FIREBASE_DATABASE_URL,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSENGER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID,
})

const authProvider = new fbauth.GoogleAuthProvider()
const firestore = getFirestore(app)

export enum UpdateType {
  Added = 'added',
  Removed = 'removed',
  Modified = 'modified',
}

interface QueryOptions {
  path: string
  orderBy?: { field: string; desc?: boolean }
  limit?: number
  startAfter?: unknown
  endAt?: unknown
  where?: { field: string | FieldPath; op: WhereFilterOp; value: unknown }[]
}

function firebaseQuery(options: QueryOptions): Query<DocumentData> {
  const params = [
    ...(options.where || []).map((opt) => where(opt.field, opt.op, opt.value)),
  ]

  if (options.orderBy) {
    params.push(
      orderBy(options.orderBy.field, options.orderBy.desc ? 'desc' : 'asc')
    )
  }

  if (options.limit) {
    params.push(limit(options.limit))
  }

  if (options.startAfter) {
    params.push(startAfter(options.startAfter))
  }

  if (options.endAt) {
    params.push(endAt(options.endAt))
  }

  return query(collection(firestore, options.path), ...params)
}

export function fetch(
  options: QueryOptions
): Promise<QuerySnapshot<DocumentData>> {
  return getDocs(firebaseQuery(options))
}

export interface Update {
  id: string
  type: UpdateType
  doc: QueryDocumentSnapshot<DocumentData>
}

export function subscribe(
  options: QueryOptions,
  callback: (error?: Error, updates?: Update[]) => void
) {
  return onSnapshot(
    firebaseQuery(options),
    (snapshot) => {
      callback(
        undefined,
        snapshot.docChanges().map((change) => {
          return {
            id: change.doc.id,
            doc: change.doc,
            type:
              change.type === 'removed'
                ? UpdateType.Removed
                : change.type === 'modified'
                ? UpdateType.Modified
                : UpdateType.Added,
          }
        })
      )
    },
    (error) => {
      callback(error)
    }
  )
}

export async function get(path: string): Promise<DocumentData | undefined> {
  const snapshot = await getSnapshot(path)
  if (!snapshot || !snapshot.exists()) {
    return
  }

  return snapshot.data()
}

export async function getSnapshot(
  path: string
): Promise<DocumentSnapshot<DocumentData> | undefined> {
  const snapshot = await getDoc(doc(firestore, path))
  if (snapshot.exists()) {
    return snapshot
  }

  return undefined
}

export function set(path: string, value: Partial<unknown>): Promise<void> {
  return setDoc(doc(firestore, path), value, {
    merge: true,
  })
}

export function add(
  path: string,
  value: Partial<unknown>
): Promise<DocumentReference> {
  return addDoc(collection(firestore, path), value)
}

export interface Session {
  user: fbauth.User
  token?: string
}

export function login(): Promise<Session | undefined> {
  return new Promise((resolve, reject) => {
    const session = remember()
    if (session) {
      return resolve(session)
    }

    const auth = fbauth.getAuth()

    if (auth.currentUser !== null) {
      return remember({
        user: auth.currentUser,
      })
    }

    // https://firebase.google.com/docs/auth/web/google-signin
    fbauth
      .getRedirectResult(auth)
      .then((result) => {
        if (!result) {
          fbauth.signInWithPopup(auth, authProvider)
          return
        }

        const credential =
          fbauth.GoogleAuthProvider.credentialFromResult(result)
        const session = {
          user: result.user,
          token: credential?.accessToken,
        }

        resolve(remember(session))
      })
      .catch(reject)
  })
}

function remember(session?: Session): Session | undefined {
  if (session) {
    localStorage.setItem('session', JSON.stringify(session))
    return session
  }

  const saved = localStorage.getItem('session')
  if (!saved || !saved.trim()) return

  try {
    return JSON.parse(saved)
  } catch (err) {
    localStorage.setItem('session', '')
    return
  }
}

export function chatMessagesPath(state: RootState): string {
  return ['orgs', state.session.orgId, 'chatMessages'].join('/')
}

export function chatMessagePath(state: RootState, messageId: string): string {
  return [chatMessagesPath(state), messageId].join('/')
}

export function remindersPath(state: RootState): string {
  return [
    'orgs',
    state.session.orgId,
    'users',
    state.session.id,
    'reminders',
  ].join('/')
}

export function reminderPath(state: RootState, id: string): string {
  return [
    'orgs',
    state.session.orgId,
    'users',
    state.session.id,
    'reminders',
    id,
  ].join('/')
}
