import { createSlice, PayloadAction } from '@reduxjs/toolkit'

export const name = 'Channel'

interface State {
  viewingDiscussion: boolean
  chatThreadId?: string
}

export const initialState: { [channel: string]: State } = {}

export const slice = createSlice({
  name,
  initialState,
  reducers: {
    openDiscussion: (state, action: PayloadAction<string>) => {
      state[action.payload] = {
        ...state[action.payload],
        viewingDiscussion: true,
      }
    },
    openChat: (state, action: PayloadAction<string>) => {
      state[action.payload] = {
        ...state[action.payload],
        viewingDiscussion: false,
      }
    },
    toggleDiscussionView: (state, action: PayloadAction<string>) => {
      state[action.payload] = {
        ...state[action.payload],
        viewingDiscussion: !state[action.payload]?.viewingDiscussion,
      }
    },
    setChatThreadId: (
      state,
      action: PayloadAction<{ channelId: string; threadId?: string }>
    ) => {
      state[action.payload.channelId] = {
        ...state[action.payload.channelId],
        chatThreadId: action.payload.threadId,
      }
    },
  },
})

export const {
  openChat,
  openDiscussion,
  toggleDiscussionView,
  setChatThreadId,
} = slice.actions
export default slice.reducer
