import CommandPalette from 'features/CommandPalette'
import { Subscriber as ReminderSubscriber } from 'features/Reminders'
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from '../../app/state'
import { initiateSession } from '../../app/state/session'
import Channel from '../../features/Channel'
import Sidebar from '../../features/Sidebar'
import selectors from '../../selectors'
import './Main.css'

function App(): JSX.Element {
  const dispatch = useDispatch()
  const [isLoggedIn, selected] = useSelector((state) => [
    selectors.session.isLoggedIn(state),
    state.sidebar.selected,
  ])

  useEffect(() => {
    dispatch(initiateSession())
  }, [dispatch])

  if (!isLoggedIn) {
    return <></>
  }

  return (
    <div className="app">
      <div className="container">
        <CommandPalette>
          <Sidebar />
          <div className="com">
            {selected.channel ? <Channel id={selected.channel} /> : null}
          </div>
        </CommandPalette>
        <ReminderSubscriber />
      </div>
    </div>
  )
}

export default App
