import React, { useRef } from 'react'

export default function useFocus<T>(): [
  React.RefObject<T>,
  (b?: boolean) => void
] {
  const el = useRef<T>(null)
  return [el, setFocus]

  function setFocus(focus?: boolean) {
    if (!el.current) return

    setTimeout(() => {
      if (focus === false) {
        // @ts-ignore
        el.current.blur()
      } else {
        // @ts-ignore
        el.current.focus()
        // @ts-ignore
        el.current.selectionStart = el.current.value.length
        // @ts-ignore
        el.current.selectionEnd = el.current.value.length
      }
    }, 50)
  }
}
