import { createSlice, PayloadAction } from '@reduxjs/toolkit'

export const name = 'Sidebar'

interface State {
  selected: { channel?: string; dm?: string }
  channels: string[]
  dms: string[]
}

export const initialState: State = {
  selected: { channel: 'welcome' },
  channels: ['welcome', 'naff'],
  dms: [],
}

export const slice = createSlice({
  name,
  initialState,
  reducers: {
    select: (
      state,
      action: PayloadAction<{ channel?: string; dm?: string }>
    ) => {
      state.selected = action.payload
    },
  },
})

export const { select } = slice.actions
export default slice.reducer
