import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { entities } from 'app/state'
import { Reminder, setBatch, Update } from 'app/state/entities'
import { focusOnInput, loadMissingMessages } from 'features/Chat/slice'
import datetime, { DateTime } from 'lib/datetime'
import selectors from 'selectors'
import * as firebase from '../../app/firebase'
import { AppDispatch, RootState } from '../../app/state/store'

export const name = 'reminders'

interface State {
  currentReminders: string[]
}

export const initialState: State = {
  currentReminders: [],
}

export const slice = createSlice({
  name,
  initialState,
  reducers: {
    setCurrentReminders: (state, action: PayloadAction<string[]>) => {
      state.currentReminders = action.payload
    },
  },
})

export const { setCurrentReminders } = slice.actions
export default slice.reducer

export function createReminder(messageId: string, remindAt: DateTime) {
  return (dispatch: AppDispatch, getState: () => RootState) => {
    firebase.add(firebase.remindersPath(getState()), {
      chatMessageId: messageId,
      createdAt: datetime.toFirebaseTime(datetime.now()),
      remindAt: datetime.toFirebaseTime(remindAt),
    })
  }
}

export function archiveReminder(reminderId: string, channelId: string) {
  return (dispatch: AppDispatch, getState: () => RootState) => {
    const existing = selectors.reminders.getById(getState(), reminderId)

    if (!existing) return

    firebase.set(firebase.reminderPath(getState(), reminderId), {
      ...existing,
      createdAt: datetime.toFirebaseTime(existing.createdAt),
      remindAt: datetime.toFirebaseTime(existing.remindAt),
      archived: true,
    })

    dispatch(focusOnInput({ channelId }))
  }
}

export function subscribe() {
  return (dispatch: AppDispatch, getState: () => RootState) => {
    firebase.subscribe(
      {
        path: firebase.remindersPath(getState()),
        orderBy: { field: 'createdAt', desc: true },
      },
      (error?: Error, updates?: firebase.Update[]) => {
        if (error) {
          console.error(error)
          return
        }

        if (!updates) return

        dispatch(
          setCurrentReminders(mergeReminderUpdatesToState(getState(), updates))
        )

        dispatch(
          setBatch(
            updates.map((update) =>
              snapshotDocumentToReminderUpdate(update.doc)
            )
          )
        )

        const missingMessageIds = chatMessageIdsToFetch(getState(), updates)

        if (missingMessageIds.length > 0) {
          dispatch(loadMissingMessages(missingMessageIds))
        }
      }
    )
  }
}

function mergeReminderUpdatesToState(
  state: RootState,
  updates: firebase.Update[]
): string[] {
  let result: string[] = state.reminders.currentReminders.slice(0)
  const alreadyAddedIds: { [id: string]: boolean } = {}

  for (let existing of result) {
    alreadyAddedIds[existing] = true
  }

  for (let update of updates) {
    switch (update.type) {
      case firebase.UpdateType.Added:
        if (!alreadyAddedIds[update.id]) {
          result.push(update.id)
        }
        break

      case firebase.UpdateType.Removed:
        result = result.filter((id) => id !== update.id)
        break
    }
  }

  return result
}

export function snapshotDocumentToReminderUpdate(
  row: firebase.QueryDocumentSnapshot<firebase.DocumentData>
): Update {
  return {
    id: row.id,
    table: entities.Reminders,
    record: <Reminder>{
      id: row.id,
      ...row.data(),
      createdAt: datetime.fromFirebaseTime(row.data().createdAt),
      remindAt: datetime.fromFirebaseTime(row.data().remindAt),
    },
  }
}

function chatMessageIdsToFetch(state: RootState, updates: firebase.Update[]) {
  return updates
    .map((row) => snapshotDocumentToReminderUpdate(row.doc))
    .filter(
      (row) =>
        !selectors.chatMessages.getById(
          state,
          (<Reminder>row.record).chatMessageId
        )
    )
    .map((row) => (<Reminder>row.record).chatMessageId)
}
