import React, { useEffect, useState } from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import { useDispatch, useSelector } from '../../app/state'
import TextField from '../../components/TextField'
import useFocus from '../../lib/use-focus'
import selectors from '../../selectors'
import { updateMessageBody } from './slice'

interface Props {
  id: string
  channelId: string
  setFocusToInput: () => void
}

const InlineEditor = (props: Props) => {
  const dispatch = useDispatch()

  const [record] = useSelector((state) => [
    selectors.chatMessages.getById(state, props.id),
  ])

  const [draft, setDraft] = useState(record?.body || '')
  const [inputEl, setInputFocused] = useFocus<HTMLTextAreaElement>()

  useHotkeys('enter', finishEditing, {
    enableOnTags: ['TEXTAREA'],
    keydown: true,
  })
  useHotkeys('esc', onBlur, { enableOnTags: ['TEXTAREA'], keydown: true })

  useEffect(() => {
    setInputFocused()
  }, [setInputFocused])

  return (
    <div className="inline-editor">
      <TextField
        placeholder=""
        value={draft}
        inputRef={inputEl}
        onInput={onInput}
        onFocus={onFocus}
        onBlur={onBlur}
      />
    </div>
  )

  function finishEditing() {
    // fixme: edit message
    props.setFocusToInput()
    dispatch(
      updateMessageBody({
        messageId: props.id,
        channelId: props.channelId,
        body: draft,
      })
    )
  }

  function onInput(value: string) {
    setDraft(value)
  }

  function onFocus() {
    // disable writing mode shortcuts?
  }

  function onBlur() {
    // dispatch(focusOnInput({ channelId: props.channelId }))
    props.setFocusToInput()
  }
}

export default InlineEditor
