import { createSlice, PayloadAction } from '@reduxjs/toolkit'

export enum Region {
  Chat = 'chat',
  ChannelSidebar = 'channelSidebar',
}

interface State {
  current: Region
}

export const initialState: State = {
  current: Region.Chat,
}

export const slice = createSlice({
  name: 'focusRegion',
  initialState,
  reducers: {
    set: (state, action: PayloadAction<Region>) => {
      state.current = action.payload
    },
    toggle: (state) => {
      if (state.current === Region.Chat) {
        state.current = Region.ChannelSidebar
      } else if (state.current === Region.ChannelSidebar) {
        state.current = Region.Chat
        return
      }
    },
  },
})

export const { set, toggle } = slice.actions
export default slice.reducer
