import { createTransform } from 'reduxjs-toolkit-persist'
import { ChatState, LoadingStatus } from './slice'

export const ResetLoadingState = createTransform(
  // Writing IDB
  (inboundState: ChatState, key: string) => {
    let copy = { ...inboundState }

    for (let channel in copy) {
      const selectedIdx = copy[channel].messages.indexOf(
        copy[channel].selected.messageId || ''
      )
      const limit =
        copy[channel].messages.length - copy[channel].pagination.viewportLimit
      let messages = copy[channel].messages

      if (selectedIdx > 0) {
        messages = messages.slice(Math.min(selectedIdx, limit))
      } else {
        messages = messages.slice(limit)
      }

      copy[channel] = {
        ...copy[channel],
        messages,
        loading: LoadingStatus.Idle,
        hasMoreMessages: true,
      }
    }

    return copy
  },
  // Restoring state from IDB
  (outboundState: ChatState, key: string) => {
    return outboundState
  },
  // define which reducers this transform gets called for.
  { whitelist: ['chat'] }
)
