import { Placeholder } from 'components/ChatMessage'
import LoadMore from 'components/LoadMore'
import React, { useEffect, useRef, useState } from 'react'

interface Props {
  messageIds: string[]
  selectedMessageId?: string
  hasMore: boolean
  isFocused: boolean
  isLoading: boolean
  children: React.ReactNode
  customScrollEl?: React.RefObject<HTMLDivElement>
  loadMore: () => void
  setFocusToLastMessage: () => void
}

export default function ChatItemList(props: Props) {
  const containerEl = useRef<HTMLDivElement>(null)
  const [scrollY, setScrollY] = useState(0)

  // Reset scroll position when user loads more messages from channel history.
  useEffect(() => {
    if (containerEl.current) {
      containerEl.current.scrollTop = scrollY
    }
  }, [props.messageIds[0]])

  // Ensure selected message is visible when selected message changes.
  useEffect(() => {
    const scrollEl: HTMLElement | null =
      props.customScrollEl?.current || containerEl.current

    if (!scrollEl || !containerEl.current || !props.selectedMessageId) return

    const messageEl: HTMLElement | undefined = Array.prototype.slice
      .call(containerEl.current.children[0].children)
      .find((el) => el.getAttribute('data-id') === props.selectedMessageId)

    if (!messageEl) {
      props.setFocusToLastMessage()
      return console.error("Can't find selected message element")
    }

    scrollEl.scrollTop = computeScrollPosition(
      scrollEl,
      containerEl.current,
      messageEl
    )
  }, [props.selectedMessageId, props.isFocused])

  return (
    <div className="messages" ref={containerEl} onScroll={onScroll}>
      <div className="messages-inner">
        {props.hasMore ? (
          <LoadMore load={props.loadMore} loading={props.isLoading}>
            {props.messageIds.length ? (
              <>
                <Placeholder /> <Placeholder />{' '}
              </>
            ) : null}
          </LoadMore>
        ) : null}
        {props.children}
      </div>
    </div>
  )

  function onScroll() {
    if (containerEl.current) setScrollY(containerEl.current.scrollTop)
  }
}

function computeScrollPosition(
  scrollEl: HTMLElement,
  listEl: HTMLElement,
  messageEl: HTMLElement
): number {
  const listRect = scrollEl.getBoundingClientRect()
  const messageRect = messageEl.getBoundingClientRect()

  const isMessageCutFromTop = messageRect.top < listRect.top
  const isMessageCutFromBottom = messageRect.bottom > listRect.bottom

  const scrollTop = scrollEl.scrollTop

  if (isMessageCutFromTop) {
    return scrollTop - (listRect.top + 50 - messageRect.top)
  }

  if (isMessageCutFromBottom) {
    return scrollTop + (messageRect.bottom + 50 - listRect.bottom)
  }

  return scrollTop
}
