import { RootState } from 'app/state'
import type { Draft, Focus } from 'features/Chat/slice'
import datetime from 'lib/datetime'
import selectors from 'selectors'
import { ThreadState } from './slice'

const QUOTE_BODY_LIMIT = 50

export const DEFAULT_FOCUS: Focus = {
  input: true,
}

export const DEFAULT_DRAFT: Draft = {
  body: '',
  updatedAt: Date.now(),
}

export function getThreadStateById(
  state: RootState,
  threadId: string
): ThreadState {
  return (
    state.chatThreads[threadId] || {
      draft: DEFAULT_DRAFT,
      selected: DEFAULT_FOCUS,
      messages: [],
    }
  )
}

// FIXME: we may not have all messages loaded at this moment.
export function getMessagesByThreadId(
  state: RootState,
  threadId: string
): string[] {
  return [threadId].concat(getThreadStateById(state, threadId).messages)
}

export function getCurrentFocusByThreadId(
  state: RootState,
  threadId: string
): Focus {
  if (!state.chatThreads[threadId]) return DEFAULT_FOCUS
  return state.chatThreads[threadId].focus || DEFAULT_FOCUS
}

export function getDraftByThreadId(state: RootState, threadId: string): Draft {
  return state.chatThreads[threadId]?.draft || DEFAULT_DRAFT
}

export function getLastMessageId(
  state: RootState,
  threadId: string
): string | undefined {
  const messages = getMessagesByThreadId(state, threadId)
  return messages && messages[messages.length - 1]
}

export function isLastMessageEditable(
  state: RootState,
  threadId: string
): boolean {
  const lastMessageId = getLastMessageId(state, threadId)
  return lastMessageId
    ? selectors.chat.isOwnMessage(state, lastMessageId)
    : false
}

export function isFocusOnInput(state: RootState, threadId: string): boolean {
  const focus = getCurrentFocusByThreadId(state, threadId)
  return (
    selectors.focusRegion.isOnChannelSidebar(state) &&
    focus.input === true &&
    !focus.settingReminder
  )
}

export function isFocusOnMessageList(
  state: RootState,
  threadId: string
): boolean {
  const selected = getCurrentFocusByThreadId(state, threadId)
  return (
    selectors.focusRegion.isOnChannelSidebar(state) &&
    !selected.input &&
    !selected.settingReminder
  )
}

export function isMessageSelected(
  state: RootState,
  threadId: string,
  messageId: string
): boolean {
  const focus = getCurrentFocusByThreadId(state, threadId)
  return focus.messageId === messageId && !focus.input
}

export function findCurrentMessageIndex(
  state: RootState,
  threadId: string
): number {
  const selectedMessageId = getCurrentFocusByThreadId(state, threadId).messageId
  if (!selectedMessageId) return -1

  const messages = getMessagesByThreadId(state, threadId)
  return messages.indexOf(selectedMessageId)
}

export function getNextMessageId(
  state: RootState,
  threadId: string,
  jumpBy?: number
): string | undefined {
  const messages = getMessagesByThreadId(state, threadId)
  if (messages.length === 0) {
    return
  }

  const currentIndex = findCurrentMessageIndex(state, threadId)
  const nextIndex = currentIndex + (jumpBy === undefined ? 1 : jumpBy)

  if (currentIndex === -1) {
    return messages[messages.length - 1]
  }

  if (nextIndex >= messages.length || nextIndex < 0) {
    return
  }

  return messages[nextIndex]
}

// FIXME: we may not have all messages loaded at this moment.
export function findMessageCount(state: RootState, threadId: string): number {
  return getMessagesByThreadId(state, threadId).length - 1
}

export function getQuoteByMessageId(
  state: RootState,
  messageId: string
): string | undefined {
  const record = selectors.chatMessages.getById(state, messageId)
  if (!record || !record.threadId) return

  const thread = selectors.chatMessages.getById(state, record.threadId)
  if (!thread) return

  const user = selectors.users.getById(state, thread.userId)
  if (!user) return

  const body = []
  const parts = thread.body.split(/[\.\!\:\(\)\s]+/)

  for (let part of parts) {
    if (body.length < QUOTE_BODY_LIMIT) {
      body.push(part)
    }
  }

  return `${user.name}: "${body.join(' ')}"`
}

export function isMessageInThread(
  state: RootState,
  threadId: string,
  messageId: string
) {
  return selectors.chatMessages.getById(state, messageId)?.threadId === threadId
}

export function isThreadLoaded(state: RootState, threadId: string): boolean {
  return !!selectors.chatMessages.getById(state, threadId)
}

export function sortMessagesBySentDate(
  state: RootState,
  messageIds: string[]
): string[] {
  if (messageIds.length < 2) return messageIds

  const messages = messageIds.map(
    (id) =>
      selectors.chatMessages.getById(state, id) || {
        id,
        channelId: '',
        userId: '',
        body: '',
        sentAt: datetime.now(),
      }
  )

  return messages
    .sort((a, b) => {
      if (!a || !b) return 0

      if (a.sentAt.seconds === b.sentAt.seconds) {
        return a.sentAt.nanoseconds - b.sentAt.nanoseconds
      }

      return a.sentAt.seconds - b.sentAt.seconds
    })
    .map((m) => m.id)
}
