import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import * as firebase from 'app/firebase'
import { AppDispatch, entities, RootState } from 'app/state'
import { ChatMessages, removeBatch } from 'app/state/entities'
import { syncUserProfiles } from 'app/state/session'
import {
  buildThreadUpdateMap,
  setThreadMessages,
} from 'features/ChatThread/slice'
import { createReminder } from 'features/Reminders/slice'
import datetime, { DateTime } from 'lib/datetime'
import selectors from 'selectors'
import {
  DEFAULT_DRAFT,
  DEFAULT_FOCUS,
  getViewportLimitByChannelId,
} from './selectors'
import * as subscriptions from './subscriptions'

export const name = 'chat'

export interface Focus {
  messageId?: string
  editing?: boolean
  input?: boolean
  settingReminder?: boolean
}

export interface Draft {
  body: string
  updatedAt: number
  remindAt?: DateTime
  threadId?: string
}

interface NewMessage {
  channelId: string
  threadId?: string
  userId: string
  body: string
  sentAt: DateTime
}

interface Pagination {
  start?: string
  end?: string
  viewportLimit: number
}

export enum LoadingStatus {
  Idle = 'idle',
  Pending = 'pending',
  Succeeded = 'succeeded',
  Failed = 'failed',
}

export interface ChannelState {
  draft: Draft
  messages: string[]
  selected: Focus
  loading: LoadingStatus
  hasMoreMessages: boolean
  pagination: Pagination
}

export interface ChatState {
  [channelId: string]: ChannelState
}

export const initialState: ChatState = {}

export const slice = createSlice({
  name,
  initialState,
  reducers: {
    setLoadingStatus: (
      state,
      action: PayloadAction<{
        channelId: string
        status: LoadingStatus
        hasMoreMessages?: boolean
      }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        loading: action.payload.status,
        hasMoreMessages:
          action.payload.hasMoreMessages !== undefined
            ? action.payload.hasMoreMessages
            : current.hasMoreMessages,
      }
    },
    selectMessage: (
      state,
      action: PayloadAction<{
        channelId: string
        id: string
        editing?: boolean
        settingReminder?: boolean
      }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        selected: {
          messageId: action.payload.id,
          settingReminder: action.payload.settingReminder,
          editing: action.payload.editing,
        },
      }
    },
    focusOnInput: (state, action: PayloadAction<{ channelId: string }>) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        selected: {
          input: true,
        },
      }
    },
    cleanDraft: (state, action: PayloadAction<{ channelId: string }>) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        draft: { body: '', updatedAt: 0 },
      }
    },
    editMessage: (
      state,
      action: PayloadAction<{ channelId: string; messageId: string }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        selected: {
          messageId: action.payload.messageId,
          editing: true,
        },
      }
    },
    setMessages: (
      state,
      action: PayloadAction<{
        channelId: string
        messageIds: string[]
        hasMore?: boolean
      }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        messages: action.payload.messageIds,
        hasMoreMessages:
          action.payload.hasMore === undefined
            ? current.hasMoreMessages
            : action.payload.hasMore,
      }
    },
    prependMessages: (
      state,
      action: PayloadAction<{
        channelId: string
        messageIds: string[]
        hasMore?: boolean
      }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        messages: action.payload.messageIds.concat(current.messages.concat()),
        hasMoreMessages:
          action.payload.hasMore === undefined
            ? current.hasMoreMessages
            : action.payload.hasMore,
      }
    },
    appendMessages: (
      state,
      action: PayloadAction<{ channelId: string; messageIds: string[] }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        messages: current.messages.concat(action.payload.messageIds),
      }
    },
    removeMessages: (
      state,
      action: PayloadAction<{ channelId: string; messageIds: string[] }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        messages: current.messages.filter(
          (id) => !action.payload.messageIds.includes(id)
        ),
      }
    },
    updateDraft: (
      state,
      action: PayloadAction<{ channelId: string; body: string }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        draft: {
          ...current.draft,
          body: action.payload.body,
          updatedAt: Date.now(),
        },
      }
    },
    setReminderForMessage: (
      state,
      action: PayloadAction<{ channelId: string; messageId: string }>
    ) => {
      state[action.payload.channelId] = {
        ...state[action.payload.channelId],
        selected: {
          messageId: action.payload.messageId,
          settingReminder: true,
        },
      }
    },
    setReminderForDraft: (
      state,
      action: PayloadAction<{ channelId: string }>
    ) => {
      state[action.payload.channelId] = {
        ...state[action.payload.channelId],
        selected: {
          ...state[action.payload.channelId].selected,
          settingReminder: true,
        },
      }
    },
    createReminderForDraft: (
      state,
      action: PayloadAction<{ channelId: string; remindAt: DateTime }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        draft: {
          ...current.draft,
          remindAt: action.payload.remindAt,
        },
      }
    },
    cleanupDraftReminder: (
      state,
      action: PayloadAction<{ channelId: string }>
    ) => {
      const current = channelState(state, action.payload.channelId)

      state[action.payload.channelId] = {
        ...current,
        draft: {
          ...current.draft,
          remindAt: undefined,
        },
      }
    },
  },
})

export const {
  setLoadingStatus,
  selectMessage,
  focusOnInput,
  cleanDraft,
  updateDraft,
  setMessages,
  prependMessages,
  appendMessages,
  removeMessages,
  editMessage,
  setReminderForMessage,
  setReminderForDraft,
  createReminderForDraft,
  cleanupDraftReminder,
} = slice.actions
export default slice.reducer

// Async thunks

// Start listening changes from most recent to oldest message in given channel.
// This should be called every time a channel loads more in history, in order to be able to
// pull updates (modifications, deletions) in the loaded messages.
export function ensureSubscription(channelId: string) {
  return async (dispatch: AppDispatch, getState: () => RootState) => {
    const state = getState()
    const limit = getViewportLimitByChannelId(state, channelId)
    const currentSize = selectors.chat.getTotalMessagesByChannelId(
      state,
      channelId
    )
    const oldestDocId = selectOldestMessageId(state, channelId)
    const alreadyAddedIds: string[] = []

    let cursor

    if (oldestDocId && currentSize >= limit) {
      cursor = await firebase.getSnapshot(
        firebase.chatMessagePath(state, oldestDocId)
      )
    } else {
      const mostRecent = await firebase.fetch({
        path: firebase.chatMessagesPath(state),
        where: [{ field: 'channelId', op: '==', value: channelId }],
        orderBy: { field: 'sentAt', desc: true },
        limit,
      })

      cursor = mostRecent.docs[mostRecent.docs.length - 1]
      alreadyAddedIds.push(...mostRecent.docs.map((d) => d.id))

      dispatch(
        setMessages({
          channelId,
          messageIds: mostRecent.docs.map((d) => d.id).reverse(),
          hasMore: mostRecent.docs.length === limit,
        })
      )

      dispatch(
        entities.setBatch(mostRecent.docs.map(snapshotDocumentToChatMessage))
      )
    }

    const unsubscribe = firebase.subscribe(
      {
        path: firebase.chatMessagesPath(getState()),
        where: [{ field: 'channelId', op: '==', value: channelId }],
        orderBy: { field: 'sentAt', desc: true },
        endAt: cursor,
      },
      (error?: Error, updates?: firebase.Update[]) => {
        if (error) {
          console.error('subscription error', error)
          return
        }

        if (!updates) return

        const state = getState()

        const [entityUpdates, toAppend, toRemove] = processUpdates(
          updates,
          buildAlreadyAddedDict(
            ...alreadyAddedIds.concat(
              channelState(state.chat, channelId).messages
            )
          )
        )

        if (toAppend.length) {
          dispatch(appendMessages({ channelId, messageIds: toAppend }))
        }

        if (toRemove.length) {
          dispatch(removeMessages({ channelId, messageIds: toRemove }))
          dispatch(
            removeBatch(
              toRemove.map((id) => {
                return {
                  table: ChatMessages,
                  id,
                }
              })
            )
          )
        }

        dispatch(entities.setBatch(entityUpdates))
        dispatch(
          syncUserProfiles(updates.map((update) => update.doc.data().userId))
        )

        dispatch(setThreadMessages(buildThreadUpdateMap(state, updates)))

        dispatch(
          setLoadingStatus({
            channelId,
            status: LoadingStatus.Succeeded,
          })
        )
      }
    )

    subscriptions.attachListener(channelId, unsubscribe)
  }
}

export function postMessage(draft: Draft) {
  return async (dispatch: AppDispatch, getState: () => RootState) => {
    const state = getState() as RootState
    const channelId = state.sidebar.selected.channel

    if (!channelId || !state.session.id) return

    const newMsg: NewMessage = {
      channelId: channelId,
      userId: state.session.id,
      body: draft.body,
      sentAt: datetime.now(),
    }

    if (draft.threadId) {
      newMsg.threadId = draft.threadId
    }

    const posted = await firebase.add(firebase.chatMessagesPath(state), {
      ...newMsg,
      sentAt: datetime.toFirebaseTime(newMsg.sentAt),
    })

    if (!draft.remindAt) return

    dispatch(createReminder(posted.id, draft.remindAt))
  }
}

export function updateMessageBody(payload: {
  messageId: string
  channelId: string
  body: string
}) {
  return async (dispatch: AppDispatch, getState: () => RootState) => {
    const state = getState()
    const record = selectors.chatMessages.getById(state, payload.messageId)

    if (!record)
      throw new Error(
        "Can't find matching message id in the store: " + payload.messageId
      )

    if (record.body.trim() === payload.body.trim()) return

    const updated = {
      ...record,
      body: payload.body,
      edited: true,
      updatedAt: datetime.now(),
    }

    const { sentAt, ...fbpayload } = updated

    await firebase.set(firebase.chatMessagePath(state, payload.messageId), {
      ...fbpayload,
      updatedAt: datetime.toFirebaseTime(updated.updatedAt),
    })

    dispatch(
      entities.set({
        table: entities.ChatMessages,
        id: payload.messageId,
        record: updated,
      })
    )
  }
}

export function loadMoreMessages(channelId: string) {
  return async (dispatch: AppDispatch, getState: () => RootState) => {
    const state = getState() as RootState

    if (state.chat[channelId]?.loading === LoadingStatus.Pending) {
      return
    }

    const oldestMessageId = selectOldestMessageId(state, channelId)

    dispatch(setLoadingStatus({ channelId, status: LoadingStatus.Pending }))

    let rows, cursor

    try {
      if (oldestMessageId !== undefined) {
        cursor = await firebase.getSnapshot(
          firebase.chatMessagePath(state, oldestMessageId)
        )
      }

      rows = await firebase.fetch({
        path: firebase.chatMessagesPath(state),
        where: [{ field: 'channelId', op: '==', value: channelId }],
        orderBy: { field: 'sentAt', desc: true },
        startAfter: cursor,
        limit: channelState(state.chat, channelId).pagination.viewportLimit,
      })
    } catch (err) {
      dispatch(setLoadingStatus({ channelId, status: LoadingStatus.Failed }))
      console.error(err)
      return
    }

    dispatch(
      prependMessages({
        channelId,
        messageIds: rows.docs.map((row) => row.id).reverse(),
      })
    )

    dispatch(entities.setBatch(rows.docs.map(snapshotDocumentToChatMessage)))
    dispatch(syncUserProfiles(rows.docs.map((doc) => doc.data().userId)))

    dispatch(
      setLoadingStatus({
        channelId,
        status: LoadingStatus.Succeeded,
        hasMoreMessages:
          channelState(state.chat, channelId).pagination.viewportLimit ===
          rows.size,
      })
    )

    dispatch(ensureSubscription(channelId))
  }
}

export function loadMissingMessages(messageIds: string[]) {
  return async (dispatch: AppDispatch, getState: () => RootState) => {
    const state = getState() as RootState

    let rows

    try {
      rows = await firebase.fetch({
        path: firebase.chatMessagesPath(state),
        where: [{ field: firebase.documentId(), op: 'in', value: messageIds }],
      })
    } catch (err) {
      console.error(err)
      return
    }

    dispatch(entities.setBatch(rows.docs.map(snapshotDocumentToChatMessage)))
    dispatch(syncUserProfiles(rows.docs.map((doc) => doc.data().userId)))
  }
}

export function jumpToMessage(channelId: string, messageId: string) {
  return async (dispatch: AppDispatch, getState: () => RootState) => {
    const cursor = await firebase.getSnapshot(
      firebase.chatMessagePath(getState(), messageId)
    )

    const rows = await firebase.fetch({
      path: firebase.chatMessagesPath(getState()),
      where: [{ field: 'channelId', op: '==', value: channelId }],
      orderBy: { field: 'sentAt', desc: true },
      endAt: cursor,
    })

    dispatch(entities.setBatch(rows.docs.map(snapshotDocumentToChatMessage)))
    dispatch(
      setMessages({
        channelId,
        messageIds: rows.docs.map((d) => d.id).reverse(),
      })
    )

    dispatch(selectMessage({ channelId, id: messageId }))
  }
}

function channelState(
  state: { [k: string]: ChannelState },
  channelId: string
): ChannelState {
  return state[channelId]
    ? state[channelId]
    : {
        messages: [],
        selected: DEFAULT_FOCUS,
        draft: DEFAULT_DRAFT,
        loading: LoadingStatus.Idle,
        hasMoreMessages: true,
        pagination: {
          viewportLimit: 10,
        },
      }
}

function selectOldestMessageId(
  state: RootState,
  channelId: string
): string | undefined {
  if (!state.chat[channelId] || !state.chat[channelId].messages) return

  return state.chat[channelId].messages.filter(
    (id) => state.entities[ChatMessages][id]
  )[0]
}

export function snapshotDocumentToChatMessage(
  row: firebase.QueryDocumentSnapshot<firebase.DocumentData>
): entities.Update {
  return {
    table: entities.ChatMessages,
    id: row.id,
    record: <entities.ChatMessage>{
      id: row.id,
      ...row.data(),
      sentAt: datetime.fromFirebaseTime(row.data().sentAt),
      updatedAt: row.data().updatedAt
        ? datetime.fromFirebaseTime(row.data().updatedAt)
        : undefined,
    },
  }
}

function processUpdates(
  updates: firebase.Update[],
  alreadyAdded: { [id: string]: boolean }
): [entities.Update[], string[], string[]] {
  const entityUpdates = updates
    .filter(
      (update) =>
        update.type === firebase.UpdateType.Added ||
        firebase.UpdateType.Modified
    )
    .map((update) => snapshotDocumentToChatMessage(update.doc))

  const toRemove = updates
    .filter((update) => update.type === firebase.UpdateType.Removed)
    .map((update) => update.id)

  const toAppend = updates
    .filter((update) => update.type === firebase.UpdateType.Added)
    .filter((update) => !alreadyAdded[update.id])
    .map((update) => update.id)
    .reverse()

  return [entityUpdates, toAppend, toRemove]
}

function buildAlreadyAddedDict(...ids: string[]): { [id: string]: boolean } {
  const result: { [id: string]: boolean } = {}

  for (let id of ids) {
    result[id] = true
  }

  return result
}
