import datetime, { DateTime } from 'lib/datetime'
import ChatMessage, { ChatMessageProps } from './index'

interface Props extends ChatMessageProps {
  returnedAt: DateTime
  messageId: string
  jumpToMessage: () => void
}

export default function RemindedChatMessage(props: Props) {
  return (
    <ChatMessage
      id={props.id}
      selected={props.selected}
      username={props.username}
      editing={false}
      reminder={props.returnedAt}
      reminderReturned={datetime.isBefore(props.returnedAt, datetime.now())}
      sentAt={props.sentAt}
      linkCaption={'Go to message'}
      onClick={props.onClick}
      onClickUsername={props.onClickUsername}
      onClickLink={props.jumpToMessage}
    >
      {props.children}
    </ChatMessage>
  )
}
