import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import * as firebase from 'app/firebase'
import { AppDispatch, entities, RootState } from 'app/state'
import { syncUserProfiles } from 'app/state/session'
import {
  Draft,
  Focus as ChatFocus,
  snapshotDocumentToChatMessage,
} from 'features/Chat/slice'
import { DateTime } from 'lib/datetime'
import {
  DEFAULT_DRAFT,
  DEFAULT_FOCUS,
  sortMessagesBySentDate,
} from './selectors'

export const name = 'chatThreads'

export type ThreadFocus = ChatFocus

export interface ThreadState {
  focus: ThreadFocus
  draft: Draft
  messages: string[]
}

const initialState: { [threadId: string]: ThreadState } = {}

export const slice = createSlice({
  name,
  initialState,
  reducers: {
    setThreadMessages: (
      state,
      action: PayloadAction<{ [threadId: string]: string[] }>
    ) => {
      for (let threadId in action.payload) {
        state[threadId] = {
          ...threadState(state, threadId),
          messages: action.payload[threadId],
        }
      }
    },
    selectMessage: (
      state,
      action: PayloadAction<{
        threadId: string
        messageId: string
        editing?: boolean
      }>
    ) => {
      const current = threadState(state, action.payload.threadId)

      state[action.payload.threadId] = {
        ...current,
        focus: { messageId: action.payload.messageId },
      }
    },
    focusOnInput: (state, action: PayloadAction<{ threadId: string }>) => {
      const current = threadState(state, action.payload.threadId)

      state[action.payload.threadId] = {
        ...current,
        focus: { input: true },
      }
    },
    cleanDraft: (state, action: PayloadAction<{ threadId: string }>) => {
      const current = threadState(state, action.payload.threadId)

      state[action.payload.threadId] = {
        ...current,
        draft: { body: '', updatedAt: 0 },
      }
    },
    updateDraft: (
      state,
      action: PayloadAction<{ threadId: string; body: string }>
    ) => {
      const current = threadState(state, action.payload.threadId)

      state[action.payload.threadId] = {
        ...current,
        draft: {
          ...current.draft,
          threadId: action.payload.threadId,
          body: action.payload.body,
          updatedAt: Date.now(),
        },
      }
    },
    editMessage: (
      state,
      action: PayloadAction<{ threadId: string; messageId: string }>
    ) => {
      const current = threadState(state, action.payload.threadId)

      state[action.payload.threadId] = {
        ...current,
        focus: {
          messageId: action.payload.messageId,
          editing: true,
        },
      }
    },
    setReminderForMessage: (
      state,
      action: PayloadAction<{ channelId: string; messageId: string }>
    ) => {
      state[action.payload.channelId] = {
        ...state[action.payload.channelId],
        focus: {
          messageId: action.payload.messageId,
          settingReminder: true,
        },
      }
    },
    setReminderForDraft: (
      state,
      action: PayloadAction<{ threadId: string }>
    ) => {
      state[action.payload.threadId] = {
        ...state[action.payload.threadId],
        focus: {
          ...state[action.payload.threadId].focus,
          settingReminder: true,
        },
      }
    },
    createReminderForDraft: (
      state,
      action: PayloadAction<{ threadId: string; remindAt: DateTime }>
    ) => {
      const current = threadState(state, action.payload.threadId)

      state[action.payload.threadId] = {
        ...current,
        draft: {
          ...current.draft,
          remindAt: action.payload.remindAt,
        },
      }
    },
    cleanupDraftReminder: (
      state,
      action: PayloadAction<{ threadId: string }>
    ) => {
      const current = threadState(state, action.payload.threadId)

      state[action.payload.threadId] = {
        ...current,
        draft: {
          ...current.draft,
          remindAt: undefined,
        },
      }
    },
  },
})

export const {
  setThreadMessages,
  selectMessage,
  focusOnInput,
  cleanDraft,
  updateDraft,
  editMessage,
  setReminderForMessage,
  setReminderForDraft,
  createReminderForDraft,
  cleanupDraftReminder,
} = slice.actions
export default slice.reducer

function threadState(
  state: { [k: string]: ThreadState },
  threadId: string
): ThreadState {
  return state[threadId]
    ? state[threadId]
    : {
        focus: DEFAULT_FOCUS,
        draft: DEFAULT_DRAFT,
        messages: [],
      }
}

export function loadMessagesByThreadId(threadId: string) {
  return async (dispatch: AppDispatch, getState: () => RootState) => {
    const state = getState() as RootState

    let rows, threadMessage

    try {
      ;[rows, threadMessage] = await Promise.all([
        firebase.fetch({
          path: firebase.chatMessagesPath(state),
          where: [{ field: 'threadId', op: '==', value: threadId }],
        }),
        firebase.fetch({
          path: firebase.chatMessagesPath(state),
          where: [{ field: firebase.documentId(), op: '==', value: threadId }],
        }),
      ])
    } catch (err) {
      console.error(err)
      return
    }

    const all = rows.docs.concat(threadMessage.docs)

    dispatch(entities.setBatch(all.map(snapshotDocumentToChatMessage)))
    dispatch(syncUserProfiles(all.map((doc) => doc.data().userId)))
    dispatch(setThreadMessages({ [threadId]: rows.docs.map((doc) => doc.id) }))
  }
}

// Take state and a set of firebase snapshot docs, return a dictionary of thread ids
// and their corresponding messages sorted by their sent date.
export function buildThreadUpdateMap(
  state: RootState,
  updates: firebase.Update[]
) {
  const result: { [threadId: string]: string[] } = {}

  for (let update of updates) {
    const threadId: string | undefined = update.doc.data().threadId

    if (!threadId) continue

    if (!result[threadId]) {
      result[threadId] = threadState(
        state.chatThreads,
        threadId
      ).messages.slice()
    }

    switch (update.type) {
      case firebase.UpdateType.Added:
        if (!result[threadId].includes(update.id)) {
          result[threadId].push(update.id)
        }

        break
      case firebase.UpdateType.Removed:
        result[threadId] = result[threadId].filter((id) => id !== update.id)
        break
    }
  }

  for (let threadId in result) {
    result[threadId] = sortMessagesBySentDate(state, result[threadId])
  }

  return result
}
