import { useDispatch, useSelector } from 'app/state'
import ChatMessage, { Placeholder } from 'components/ChatMessage'
import { setChatThreadId } from 'features/Channel/slice'
import InlineEditor from 'features/Chat/InlineEditor'
import { editMessage, Focus } from 'features/Chat/slice'
import FocusRegion from 'features/FocusRegion'
import React from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import selectors from 'selectors'
import {
  editMessage as editMessageInThread,
  focusOnInput as focusOnThreadInput,
} from '../ChatThread/slice'

interface Props {
  id: string
  channelId: string
  selected?: Focus
  shownInThread?: boolean
  nextMessageId?: string
  previousMessageId?: string
  setFocusToInput: () => void
  onClick: () => void
}

export default function ChatMessageItem(props: Props) {
  const dispatch = useDispatch()

  const [
    message,
    user,
    reminder,
    isEditable,
    replyCount,
    quote,
    nextMsgInThread,
    prevMsgInThread,
    threadIdOnChannelSidebar,
  ] = useSelector((state) => {
    const message = selectors.chatMessages.getById(state, props.id)
    const nextMsgInThread =
      message?.threadId &&
      props.nextMessageId &&
      selectors.chatThreads.isMessageInThread(
        state,
        message.threadId,
        props.nextMessageId
      )

    const prevMsgInThread =
      message?.threadId &&
      props.previousMessageId &&
      selectors.chatThreads.isMessageInThread(
        state,
        message?.threadId,
        props.previousMessageId
      )

    return [
      message,
      message && selectors.users.getById(state, message.userId),
      selectors.reminders.getLatestNotReturnedByMessageId(state, props.id),
      selectors.chat.isMessageEditable(state, props.id),
      selectors.chatThreads.findMessageCount(state, props.id),
      selectors.chatThreads.getQuoteByMessageId(state, props.id),
      nextMsgInThread,
      prevMsgInThread,
      selectors.channels.getCurrentChatThreadId(state, props.channelId),
    ]
  })

  useHotkeys(
    'enter',
    replyMessage,
    { keydown: true, enabled: !!props.selected },
    [props.id, message?.threadId, threadIdOnChannelSidebar, props.shownInThread]
  )

  useHotkeys('.', edit, { keydown: true, enabled: !!props.selected }, [
    props.selected?.messageId,
    isEditable,
  ])

  if (!message) return <Placeholder />

  const editing = props.selected && props.selected.editing
  const [linkCaption, onClickLink] = threadLinkProps()

  return (
    <ChatMessage
      id={props.id}
      editing={props.selected && props.selected.editing}
      linkCaption={linkCaption}
      username={user?.name || ''}
      sentAt={message.sentAt}
      reminder={reminder?.remindAt}
      quote={!props.shownInThread && !prevMsgInThread ? quote : ''}
      selected={!!props.selected}
      onClick={props.onClick}
      onClickUsername={onClickUsername}
      onClickLink={onClickLink}
    >
      {!editing ? (
        message.body || ''
      ) : (
        <InlineEditor
          id={props.id}
          channelId={props.channelId}
          setFocusToInput={props.setFocusToInput}
        />
      )}
      {!editing && message.edited ? (
        <span className="edited">(edited)</span>
      ) : null}
    </ChatMessage>
  )

  function onClickUsername() {}

  function replyMessage() {
    if (props.shownInThread) {
      props.setFocusToInput()
      return
    }

    // The message is both on main and channel sidebar.
    if (message?.threadId && message.threadId === threadIdOnChannelSidebar) {
      dispatch(focusOnThreadInput({ threadId: message.threadId }))
      dispatch(FocusRegion.set(FocusRegion.Region.ChannelSidebar))
      return
    }

    // Open message in the sidebar
    dispatch(
      setChatThreadId({
        channelId: props.channelId,
        threadId: message?.threadId || message?.id,
      })
    )

    dispatch(FocusRegion.set(FocusRegion.Region.ChannelSidebar))
  }

  function edit() {
    if (!isEditable) return

    if (!props.shownInThread) {
      dispatch(
        editMessage({
          channelId: props.channelId,
          messageId: props.id,
        })
      )
    } else if (message?.threadId) {
      dispatch(
        editMessageInThread({
          threadId: message?.threadId,
          messageId: props.id,
        })
      )
    }
  }

  function threadLinkProps(): [string | undefined, (() => void) | undefined] {
    if (message?.threadId && !props.shownInThread && !nextMsgInThread) {
      return ['View Thread', replyMessage]
    } else if (replyCount > 0 && !props.shownInThread) {
      return [
        replyCount == 1 ? `1 Reply` : `${replyCount} Replies`,
        replyMessage,
      ]
    }

    return [undefined, undefined]
  }
}
