import React, { useEffect, useRef, useState } from 'react'

interface Props {
  children: React.ReactNode
  load: () => void
  loading: boolean
}

export default function LoadMore(props: Props) {
  const el = useRef<HTMLDivElement>(null)
  const [visible, setVisible] = useState<boolean>(false)

  useEffect(() => {
    const observer = new IntersectionObserver(([entry]) => {
      setVisible(entry.isIntersecting)

      if (entry.isIntersecting && props.loading == false) {
        props.load()
      }
    })

    if (el.current) observer.observe(el.current)

    return () => {
      if (el.current) observer.unobserve(el.current)
    }
  }, [])

  useEffect(() => {
    if (!props.loading && visible) {
      props.load()
    }
  }, [props.loading, visible])

  return (
    <div ref={el} className="loadMore">
      {props.children}
    </div>
  )
}
