import { useDispatch, useSelector } from 'app/state'
import ChatMessageList from 'components/ChatMessageList'
import Input from 'features/Chat/Input'
import { postMessage } from 'features/Chat/slice'
import ChatItem from 'features/ChatItem'
import focusRegion from 'features/FocusRegion'
import Reminders from 'features/Reminders'
import { DateTime } from 'lib/datetime'
import React, { useEffect, useRef, useState } from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import selectors from 'selectors'
import {
  cleanDraft,
  cleanupDraftReminder,
  createReminderForDraft,
  editMessage,
  focusOnInput,
  loadMessagesByThreadId,
  selectMessage,
  setReminderForDraft,
  updateDraft,
} from './slice'

interface Props {
  channelId: string
  threadId: string
}

export default function ChatThread(props: Props) {
  const dispatch = useDispatch()
  const scrollEl = useRef<HTMLDivElement>(null)
  const [scrollY, setScrollY] = useState(0)

  const [
    draft,
    messages,
    selected,
    nextMessageId,
    prevMessageId,
    lastMessageId,
    isLastMessageEditable,
    isFocusOnThread,
    isFocusOnInput,
    isFocusOnMessageList,
  ] = useSelector((state) => [
    selectors.chatThreads.getDraftByThreadId(state, props.threadId),
    selectors.chatThreads.getMessagesByThreadId(state, props.threadId),
    selectors.chatThreads.getCurrentFocusByThreadId(state, props.threadId),
    selectors.chatThreads.getNextMessageId(state, props.threadId),
    selectors.chatThreads.getNextMessageId(state, props.threadId, -1),
    selectors.chatThreads.getLastMessageId(state, props.threadId),
    selectors.chatThreads.isLastMessageEditable(state, props.threadId),
    selectors.focusRegion.isOnChannelSidebar(state),
    selectors.chatThreads.isFocusOnInput(state, props.threadId),
    selectors.chatThreads.isFocusOnMessageList(state, props.threadId),
  ])

  useHotkeys('k, up, cmd+up', previous, { enabled: isFocusOnMessageList }, [
    selected,
  ])
  useHotkeys('j, down, cmd+down', next, { enabled: isFocusOnMessageList }, [
    selected,
  ])

  useHotkeys('j, down, cmd+down', next, { enabled: isFocusOnMessageList }, [
    selected,
  ])

  useHotkeys('cmd+d', toggleBetweenPanes, {
    enabled: isFocusOnThread,
    enableOnTags: ['TEXTAREA'],
  })

  useHotkeys('esc', onPressEsc, {
    enableOnTags: ['TEXTAREA'],
    enabled: isFocusOnThread,
  })

  useEffect(() => {
    if (scrollY <= 25 && scrollEl.current) {
      scrollEl.current.scrollTop = scrollEl.current.scrollHeight
    }
  }, [messages.length])

  useEffect(() => {
    dispatch(loadMessagesByThreadId(props.threadId))
  }, [props.threadId])

  return (
    <div className="thread">
      <header>
        Thread <span>#{props.channelId}</span>
      </header>
      <Reminders
        enabled={isFocusOnThread}
        focus={selected}
        createReminderForDraft={onCreateReminderForDraft}
        setReminderForDraft={onSetReminderForDraft}
        selectMessage={setMessageSelected}
        setFocusToInput={setFocusToInput}
        selectLastMessage={lastMessageId ? selectLastMessage : undefined}
      >
        <div className="content" ref={scrollEl} onScroll={onScroll}>
          <ChatMessageList
            messageIds={messages}
            hasMore={false}
            loadMore={loadMoreMessages}
            isLoading={false}
            selectedMessageId={selected.messageId}
            isFocused={isFocusOnMessageList}
            customScrollEl={scrollEl}
            setFocusToLastMessage={selectLastMessage}
          >
            {messages.map((id) => (
              <ChatItem
                key={id}
                itemId={id}
                channelId={props.channelId}
                setFocusToInput={setFocusToInput}
                selected={
                  isFocusOnMessageList && id === selected.messageId
                    ? selected
                    : undefined
                }
                onClick={() => setMessageSelected(id)}
                shownInThread
              />
            ))}
          </ChatMessageList>
          <Input
            enabled={isFocusOnInput}
            draft={draft}
            clearDraft={clearDraftValue}
            postDraftMessage={postDraftMessage}
            setDraftValue={setDraftValue}
            removeDraftReminder={removeDraftReminder}
            editLastMessage={editLastMessage}
            setFocusToInput={setFocusToInput}
            selectLastMessage={selectLastMessage}
            placeholder="Reply to thread"
          />
        </div>
      </Reminders>
    </div>
  )

  function loadMoreMessages() {}

  function editLastMessage() {
    if (isLastMessageEditable && lastMessageId) {
      dispatch(
        editMessage({ threadId: props.threadId, messageId: lastMessageId })
      )
    }
  }

  function previous(e: KeyboardEvent) {
    e.preventDefault()

    if (prevMessageId) {
      dispatch(
        selectMessage({ threadId: props.threadId, messageId: prevMessageId })
      )
    }
  }

  function next(e: KeyboardEvent) {
    e.preventDefault()

    if (nextMessageId) {
      dispatch(
        selectMessage({ threadId: props.threadId, messageId: nextMessageId })
      )
    } else {
      dispatch(focusOnInput({ threadId: props.threadId }))
    }
  }

  function onPressEsc(e: KeyboardEvent) {
    e.preventDefault()

    if (isFocusOnInput) {
      dispatch(focusRegion.toggle())
    } else {
      dispatch(focusOnInput({ threadId: props.threadId }))
    }
  }

  function clearDraftValue() {
    dispatch(cleanDraft({ threadId: props.threadId }))
  }

  function postDraftMessage() {
    dispatch(postMessage(draft))
  }

  function selectLastMessage() {
    if (lastMessageId) {
      dispatch(
        selectMessage({ threadId: props.threadId, messageId: lastMessageId })
      )
    }
  }

  function setMessageSelected(messageId: string) {
    dispatch(selectMessage({ threadId: props.threadId, messageId }))
    dispatch(focusRegion.set(focusRegion.Region.ChannelSidebar))
  }

  function setFocusToInput() {
    dispatch(focusOnInput({ threadId: props.threadId }))
    dispatch(focusRegion.set(focusRegion.Region.ChannelSidebar))
  }

  function setDraftValue(value: string) {
    dispatch(updateDraft({ threadId: props.threadId, body: value }))
  }

  function removeDraftReminder() {
    if (draft.remindAt) {
      dispatch(cleanupDraftReminder({ threadId: props.threadId }))
    }
  }

  function onCreateReminderForDraft(remindAt: DateTime) {
    dispatch(
      createReminderForDraft({
        threadId: props.threadId,
        remindAt,
      })
    )
  }

  function onSetReminderForDraft() {
    dispatch(
      setReminderForDraft({
        threadId: props.threadId,
      })
    )
  }

  function toggleBetweenPanes(e: KeyboardEvent) {
    e.preventDefault()
    dispatch(focusRegion.toggle())
  }

  function onScroll() {
    if (scrollEl.current)
      setScrollY(
        scrollEl.current.scrollHeight -
          scrollEl.current.scrollTop -
          scrollEl.current.clientHeight
      )
  }
}
