import { useDispatch, useSelector } from 'app/state'
import { Placeholder } from 'components/ChatMessage'
import RemindedChatMessage from 'components/ChatMessage/RemindedChatMessage'
import { setChatThreadId } from 'features/Channel/slice'
import { Focus, jumpToMessage } from 'features/Chat/slice'
import { focusOnInput as focusOnThreadInput } from 'features/ChatThread/slice'
import FocusRegion from 'features/FocusRegion'
import { archiveReminder } from 'features/Reminders/slice'
import React from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import selectors from 'selectors'

interface Props {
  id: string
  channelId: string
  selected?: Focus
  onClick: () => void
}

export default function ReturnedReminderItem(props: Props) {
  const dispatch = useDispatch()

  const [reminder, message, user, threadIdOnChannelSidebar] = useSelector(
    (state) => {
      const reminder = selectors.chatItems.getById(state, props.id)?.reminder
      const message = selectors.chatMessages.getById(
        state,
        reminder?.chatMessageId || ''
      )

      return [
        reminder,
        message,
        message && selectors.users.getById(state, message.userId),
        selectors.channels.getCurrentChatThreadId(state, props.channelId),
      ]
    }
  )

  useHotkeys(
    'o',
    onJumpToMessage,
    { keydown: true, enabled: !!props.selected },
    [message?.id]
  )

  useHotkeys(
    'enter',
    openThreadOnSidebar,
    { keydown: true, enabled: !!props.selected },
    [props.id, message?.threadId, threadIdOnChannelSidebar]
  )

  useHotkeys('e', onArchiveReminder, {
    keydown: true,
    enabled: !!props.selected,
  })

  if (!reminder || !message) return <Placeholder />

  return (
    <RemindedChatMessage
      id={props.id}
      messageId={message.id}
      username={user?.name || ''}
      sentAt={message.sentAt}
      returnedAt={reminder.remindAt}
      selected={!!props.selected}
      onClick={props.onClick}
      onClickUsername={onClickUsername}
      jumpToMessage={onJumpToMessage}
    >
      {message.body}
    </RemindedChatMessage>
  )

  function onClickUsername() {}

  function onJumpToMessage() {
    if (message) {
      dispatch(jumpToMessage(props.channelId, message.id))
    }
  }

  function openThreadOnSidebar() {
    // The message is both on main and channel sidebar.
    if (message?.threadId && message.threadId === threadIdOnChannelSidebar) {
      dispatch(focusOnThreadInput({ threadId: message.threadId }))
      dispatch(FocusRegion.set(FocusRegion.Region.ChannelSidebar))
      return
    }

    // Open message in the sidebar
    dispatch(
      setChatThreadId({
        channelId: props.channelId,
        threadId: message?.threadId || message?.id,
      })
    )

    dispatch(FocusRegion.set(FocusRegion.Region.ChannelSidebar))
  }

  function onArchiveReminder() {
    dispatch(archiveReminder(props.id, props.channelId))
  }
}
