import { Command, CommandType, ContextMatch } from './use-command-palette'

const ContextMatchScores = {
  [CommandType.ActionOnFocus]: 100,
  [CommandType.ShiftFocusWithinRegion]: 80,
  [CommandType.ShiftFocusBetweenRegions]: 60,
  [CommandType.ShiftContext]: 40,
  [ContextMatch.SameTab]: 5,
}

export function menu(all: Command[]): Command[] {
  return all
    .filter((cmd: Command) => isCommandEnabled(cmd))
    .sort(sortCommandsByRelevance)
}

function isCommandEnabled(cmd: Command): boolean {
  if (!cmd.relevancy || cmd.relevancy.length === 0) return true
  return cmd.relevancy.every((match) => match.isMatch || !match.must)
}

function sortCommandsByRelevance(a: Command, b: Command): number {
  const scoreA = calculateMatchingScore(a)
  const scoreB = calculateMatchingScore(b)
  return scoreB - scoreA
}

function calculateMatchingScore(a: Command): number {
  if (!a.relevancy) return 0

  return a.relevancy
    .map((match) => {
      if (!match.isMatch) return 0
      return ContextMatchScores[match.contextMatch]
    })
    .reduce((sum, a) => sum + a)
}
