import { createContext, useContext } from 'react'

export interface Command {
  title: string
  value: unknown
  key: string
  fn?: () => void
  enabled?: () => boolean
  relevancy?: CommandRelevancyResult[]
}

export enum CommandType {
  ActionOnFocus = 'action-on-focus',
  ShiftFocusWithinRegion = 'shift-focus-within-region',
  ShiftFocusBetweenRegions = 'shift-focus-between-regions',
  ShiftContext = 'shift-context',
}

export enum ContextMatch {
  SameTab = 'same-tab',
}

export interface CommandRelevancyResult {
  isMatch: boolean
  must: boolean
  contextMatch: CommandType | ContextMatch
}

export type RegistryFn = (
  name: string,
  commandFn: () => void,
  relevancy?: CommandRelevancyResult[]
) => void

export type DialogOptions = Omit<
  CommandPaletteContext,
  'isOpen' | 'open' | 'callback' | 'register' | 'deregister' | 'registry'
>

export function mustMatch(
  contextMatch: CommandType | ContextMatch,
  isMatch: boolean
): CommandRelevancyResult {
  return {
    isMatch,
    contextMatch,
    must: true,
  }
}

export function match(
  contextMatch: CommandType | ContextMatch,
  isMatch: boolean
): CommandRelevancyResult {
  return {
    isMatch,
    contextMatch,
    must: false,
  }
}

export interface CommandPaletteContext {
  heading: string
  placeholder: string
  menu: Command[]
  callback: ((command?: Command) => void) | undefined
  open: (options: DialogOptions, callback: (command?: Command) => void) => void
  registry: { [key: string]: Command }
  register: RegistryFn
  deregister: (...title: string[]) => void
}

export const initialOptions: CommandPaletteContext = {
  heading: '',
  placeholder: '',
  menu: [],
  open: noop,
  callback: undefined,
  registry: {},
  register: noop,
  deregister: noop,
}

export const Context = createContext<CommandPaletteContext>(initialOptions)

export default function useCommandPalette() {
  return useContext(Context)
}

function noop() {
  console.log('noop')
}
