import { useDispatch, useSelector } from 'app/state'
import { Focus } from 'features/Chat/slice'
import {
  Command,
  CommandType,
  mustMatch,
  useCommandPalette,
} from 'features/CommandPalette'
import useCommandRegistry from 'features/CommandPalette/use-command-registry'
import datetime, { DateTime } from 'lib/datetime'
import React from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import selectors from 'selectors'
import { archiveReminder, createReminder } from './slice'

export { default as Subscriber } from './Subscriber'

const HOUR = 60 * 60 * 1000

interface Props {
  children: React.ReactNode
  enabled: boolean
  focus: Focus
  selectMessage: (id: string) => void
  setFocusToInput: () => void
  createReminderForDraft: (dt: DateTime) => void
  setReminderForDraft: () => void
  selectLastMessage?: () => void
}

export default function Reminders(props: Props) {
  const dispatch = useDispatch()
  const commandPalette = useCommandPalette()
  const navigationEnabled = props.enabled && !props.focus.input
  const inputEnabled = props.enabled && props.focus.input

  const [item] = useSelector((state) => [
    selectors.chatItems.getById(state, props.focus.messageId || ''),
  ])

  const isFocusOnInput = props.enabled && !!props.focus.input
  const isFocusOnReminder = props.enabled && !!item?.reminder
  const isFocusOnMessage =
    props.enabled && !item?.reminder && !!item?.chatMessage

  useHotkeys(
    'h',
    onSetReminderForSelectedMessage,
    {
      keyup: true,
      enabled: navigationEnabled,
    },
    [props.focus]
  )

  useHotkeys('cmd+h', onSetReminderForLastMessage, {
    enableOnTags: ['TEXTAREA'],
    keydown: true,
    enabled: inputEnabled,
  })

  useHotkeys('cmd+shift+h', onSetReminderForDraft, {
    enableOnTags: ['TEXTAREA'],
    keydown: true,
    enabled: inputEnabled,
  })

  useCommandRegistry(
    (register) => {
      if (!props.enabled) return

      register('Remind me', onSetReminderForSelectedMessage, [
        mustMatch(CommandType.ActionOnFocus, isFocusOnMessage),
      ])

      register('Archive reminder', onArchiveReminder, [
        mustMatch(CommandType.ActionOnFocus, isFocusOnReminder),
      ])

      register('Set reminder for draft', onSetReminderForDraft, [
        mustMatch(CommandType.ActionOnFocus, isFocusOnInput),
      ])
    },
    [
      props.enabled,
      !!item?.reminder,
      props.focus.messageId,
      isFocusOnMessage,
      isFocusOnInput,
    ]
  )

  return <>{props.children}</>

  function onSetReminderForSelectedMessage() {
    if (props.focus.messageId) {
      promptOptions(false)
    }
  }

  function onSetReminderForLastMessage(e: KeyboardEvent) {
    e.preventDefault()

    if (!props.selectLastMessage) return

    props.selectLastMessage()
    promptOptions(true)
  }

  function onSetReminderForDraft(e?: KeyboardEvent) {
    if (e) e.preventDefault()
    props.setReminderForDraft()
    promptOptions(true)
  }

  function onSelectReminder(value: number): void {
    if (item && item.chatMessage) {
      dispatch(createReminder(item.chatMessage.id, datetime.now()))
    } else if (props.focus.input) {
      props.createReminderForDraft(datetime.now())
    }

    if (item && item.reminder && item.chatMessage) {
      onArchiveReminder()
    }
  }

  function onArchiveReminder() {
    dispatch(
      archiveReminder(item?.id || '', item?.chatMessage?.channelId || '')
    )
  }

  function promptOptions(returnFocusToInput: boolean) {
    commandPalette.open(
      {
        heading: 'Remind me',
        placeholder: '',
        menu: [
          { title: 'in 1 hour', value: 1 * HOUR, key: '' },
          { title: 'tonight', value: 1 * HOUR, key: '' }, // fixme
          { title: 'tomorrow', value: 24 * HOUR, key: '' },
          { title: 'end of week', value: 24 * HOUR, key: '' }, // fixme
        ],
      },
      (selected?: Command) => {
        if (selected) {
          onSelectReminder(selected?.value as number)
        }

        if (returnFocusToInput) {
          props.setFocusToInput()
        }
      }
    )
  }
}
