import indexeddbStorage from '@piotr-cz/redux-persist-idb-storage'
import {
  configureStore,
  createSerializableStateInvariantMiddleware,
} from '@reduxjs/toolkit'
import { ResetLoadingState as ResetChatLoadingState } from 'features/Chat/persistency'
import {
  FLUSH,
  PAUSE,
  PERSIST,
  persistCombineReducers,
  persistStore,
  PURGE,
  REGISTER,
  REHYDRATE,
} from 'reduxjs-toolkit-persist'
import autoMergeLevel2 from 'reduxjs-toolkit-persist/lib/stateReconciler/autoMergeLevel1'
import defaultStorage from 'reduxjs-toolkit-persist/lib/storage'
import logger from './logger'
import allReducers from './reducers'

const config = {
  key: 'root',
  storage: globalThis.indexedDB
    ? indexeddbStorage({
        name: process.env.REACT_APP_INDEXEDDB_NAME,
        storeName: process.env.REACT_APP_INDEXEDDB_STORE,
      })
    : defaultStorage,
  stateReconciler: autoMergeLevel2,
  serialize: false,
  deserialize: false,
  whitelist: [
    'sidebar',
    'focusRegion',
    'chatThreads',
    'session',
    'chat',
    'channel',
    'entities',
  ],
  transforms: [ResetChatLoadingState],
}

// const reducer = persistCombineReducers(config, reducers)
const ignoredActions = createSerializableStateInvariantMiddleware({
  ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
})

export const store = configureStore({
  reducer: persistCombineReducers(config, allReducers),
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware().concat(ignoredActions).concat(logger),
})

export const persistor = persistStore(store)
