import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { DateTime } from 'lib/datetime'

export type Entity = ChatMessage | User | Reminder
export type Table = typeof ChatMessages | typeof Users | typeof Reminders

export interface Update {
  table: Table
  id: string
  record: Entity
}

export const Users = 'users'
export interface User {
  id: string
  name: string
}

export const ChatMessages = 'chatMessages'
export interface ChatMessage {
  id: string
  channelId: string
  userId: string
  body: string
  sentAt: DateTime
  edited?: boolean
  unread?: boolean
  threadId?: string
  updatedAt?: DateTime
}

export const Reminders = 'reminders'
export interface Reminder {
  id: string
  chatMessageId: string
  archived?: boolean
  remindAt: DateTime
  createdAt: DateTime
}

const initialState = {
  [ChatMessages]: {} as Record<string, ChatMessage>,
  [Users]: {} as Record<string, User>,
  [Reminders]: {} as Record<string, Reminder>,
}

export const slice = createSlice({
  name: 'entities',
  initialState,
  reducers: {
    set: (state, action: PayloadAction<Update>) => {
      state[action.payload.table][action.payload.id] = action.payload.record
    },
    setBatch: (state, action: PayloadAction<Update[]>) => {
      for (let update of action.payload) {
        if (!state[update.table]) {
          state[update.table] = {}
        }

        state[update.table][update.id] = update.record
      }
    },
    removeBatch: (
      state,
      action: PayloadAction<{ table: Table; id: string }[]>
    ) => {
      for (let row of action.payload) {
        delete state[row.table][row.id]
      }
    },
  },
})

export const { set, setBatch, removeBatch } = slice.actions

export default slice.reducer
