import { RootState } from 'app/state'
import selectors from 'selectors'
import { ChannelState, Draft, Focus, LoadingStatus } from './slice'

export const DEFAULT_FOCUS: Focus = {
  input: true,
}

export const DEFAULT_DRAFT: Draft = {
  body: '',
  updatedAt: Date.now(),
}

export function getChannelStateById(
  state: RootState,
  channelId: string
): ChannelState {
  return (
    state.chat[channelId] || {
      draft: DEFAULT_DRAFT,
      messages: [],
      selected: DEFAULT_FOCUS,
      loading: LoadingStatus.Idle,
      hasMoreMessages: true,
      pagination: {
        viewportLimit: 10,
      },
    }
  )
}

export function isLoadingMessages(state: RootState, channelId: string) {
  return getChannelStateById(state, channelId).loading === LoadingStatus.Pending
}

export function hasMoreMessages(state: RootState, channelId: string) {
  return getChannelStateById(state, channelId).hasMoreMessages
}

export function getCurrentFocusByChannelId(
  state: RootState,
  channelId: string
): Focus {
  if (!state.chat[channelId]) return DEFAULT_FOCUS
  return state.chat[channelId].selected || DEFAULT_FOCUS
}

export function getMessagesByChannelId(
  state: RootState,
  channelId: string
): string[] {
  return state.chat[channelId]?.messages || []
}

export function getTotalMessagesByChannelId(
  state: RootState,
  channelId: string
): number {
  return getMessagesByChannelId(state, channelId).length
}

export function getDraftByChannelId(
  state: RootState,
  channelId: string
): Draft {
  return state.chat[channelId]?.draft || DEFAULT_DRAFT
}

export function isOwnMessage(state: RootState, messageId: string): boolean {
  return (
    selectors.chatMessages.getById(state, messageId)?.userId ===
    state.session.id
  )
}

export function isFocusOnInput(state: RootState, channelId: string): boolean {
  const focus = getCurrentFocusByChannelId(state, channelId)
  return (
    selectors.focusRegion.isOnChat(state) &&
    focus.input === true &&
    !focus.settingReminder
  )
}

export function isFocusOnMessageList(
  state: RootState,
  channelId: string
): boolean {
  const selected = getCurrentFocusByChannelId(state, channelId)

  return (
    selectors.focusRegion.isOnChat(state) &&
    !selected.input &&
    !selected.settingReminder
  )
}

export function isMessageSelected(
  state: RootState,
  channelId: string,
  messageId: string
): boolean {
  const focus = getCurrentFocusByChannelId(state, channelId)
  return focus.messageId === messageId && !focus.input
}

export function isMessageEditable(
  state: RootState,
  messageId: string
): boolean {
  return isOwnMessage(state, messageId)
}

export function getMessagesToLoad(state: RootState, channelId: string) {
  const allMessages = getMessagesByChannelId(state, channelId)
  return allMessages
    .filter((id) => !selectors.chatMessages.getById(state, id))
    .reverse()
    .slice(0, 10)
}

export function isChannelInitialized(
  state: RootState,
  channelId: string
): boolean {
  return getChannelStateById(state, channelId).loading !== LoadingStatus.Idle
}

export function isMessageShownInChannelSidebar(
  state: RootState,
  channelId: string,
  messageId: string
): boolean {
  return (
    selectors.chatMessages.getById(state, messageId)?.threadId ==
    selectors.channels.getCurrentChatThreadId(state, channelId)
  )
}

export function getViewportLimitByChannelId(
  state: RootState,
  channelId: string
): number {
  return getChannelStateById(state, channelId).pagination.viewportLimit
}
