import Placeholder from 'components/Placeholder'
import Timestamp from 'components/Timestamp'
import datetime, { DateTime } from 'lib/datetime'
import React from 'react'
import Link from './Link'
export { default as Placeholder } from './Placeholder'

export interface ChatMessageProps {
  id: string
  selected: boolean
  username: string
  sentAt: DateTime
  reminder?: DateTime
  reminderReturned?: boolean
  editing?: boolean
  linkCaption?: string
  quote?: string
  shownInGroup?: boolean

  children: React.ReactNode
  onClick: () => void
  onClickUsername: () => void
  onClickLink?: () => void
}

export default function ChatMessage(props: ChatMessageProps) {
  return (
    <>
      {props.shownInGroup && props.quote ? (
        <div className="quote-block">Replies to: {props.quote}</div>
      ) : null}

      <div
        data-id={props.id}
        className={`message ${props.selected ? 'selected' : ''}  ${
          props.editing ? 'editing' : ''
        } ${props.shownInGroup ? 'group' : ''} ${
          props.reminderReturned ? 'reminder-returned' : ''
        }`}
        onClick={props.onClick}
      >
        {props.reminder && props.reminderReturned ? (
          <div className="reminder-header">
            Reminder visible only to you{' '}
            <label>
              <Timestamp epoch={datetime.toEpoch(props.reminder)} />
            </label>
          </div>
        ) : null}
        <header>
          <label onClick={props.onClickUsername} className="username">
            {props.username || <Placeholder width={1} />}
          </label>
          <Timestamp epoch={datetime.toEpoch(props.sentAt)} relative />
          {!props.shownInGroup && props.quote ? (
            <span className="quote">{props.quote}</span>
          ) : null}
        </header>
        <div className="body">{props.children}</div>
        {props.linkCaption && props.onClickLink ? (
          <Link onClick={props.onClickLink}>{props.linkCaption}</Link>
        ) : null}
        {props.reminder && !props.reminderReturned ? (
          <div className="reminder">
            Remind me: <Timestamp epoch={datetime.toEpoch(props.reminder)} />
          </div>
        ) : null}
        <div className="border"></div>
        {props.reminderReturned ? <div className="quote-border"></div> : null}
      </div>
    </>
  )
}
