import { openChat, openDiscussion } from 'features/Channel/slice'
import {
  CommandType,
  ContextMatch,
  match,
  mustMatch,
} from 'features/CommandPalette/use-command-palette'
import useCommandRegistry from 'features/CommandPalette/use-command-registry'
import React from 'react'
import selectors from 'selectors'
import { useDispatch, useSelector } from '../../app/state'
import Folder from './Folder'
import { select } from './slice'

const folders = [{ name: 'Inbox' }, { name: 'Sent' }, { name: 'Done' }]

export default function Sidebar() {
  const dispatch = useDispatch()

  const [selected, channels, dms, isFocusOnChatTab] = useSelector((state) => [
    state.sidebar.selected,
    state.sidebar.channels,
    state.sidebar.dms,
    state.sidebar.selected.channel
      ? selectors.channels.isFocusOnChatTab(
          state,
          state.sidebar.selected.channel
        )
      : false,
  ])

  useCommandRegistry(
    (register) => {
      for (let channel of channels) {
        register(`#${channel} Chat`, goToChatChannel(channel), [
          mustMatch(CommandType.ShiftContext, selected.channel !== channel),
          match(ContextMatch.SameTab, isFocusOnChatTab),
        ])

        register(`#${channel} Discussions`, goToDiscussionChannel(channel), [
          mustMatch(CommandType.ShiftContext, selected.channel !== channel),
          match(ContextMatch.SameTab, !isFocusOnChatTab),
        ])

        if (selected.channel === channel) {
          register(
            `#${channel} Discussion`,
            () => dispatch(openDiscussion(channel)),
            [mustMatch(CommandType.ShiftFocusWithinRegion, isFocusOnChatTab)]
          )

          register(`#${channel} Chat`, () => dispatch(openChat(channel)), [
            mustMatch(CommandType.ShiftFocusWithinRegion, !isFocusOnChatTab),
          ])
        }
      }
    },
    [selected.channel, channels, isFocusOnChatTab]
  )

  return (
    <div className="navigation">
      <div className="logo">Superhuman</div>
      <div className="folders">
        {folders.map((f) => (
          <Folder key={f.name}>{f.name}</Folder>
        ))}
        <Folder inactive>More</Folder>
      </div>
      <Separator />
      <div className="channels">
        {channels.map((channel) => (
          <Folder
            key={channel}
            selected={selected.channel === channel}
            inactive={isFolderRead(channel)}
            channel
            onClick={() => dispatch(select({ channel }))}
          >
            {!isFolderRead(channel) ? <UnreadIcon /> : null} #{channel}
          </Folder>
        ))}
      </div>
      <div className="dms">
        <h3>Direct Messages</h3>
        {dms.map((dm) => (
          <Folder
            key={dm}
            selected={selected.dm === dm}
            inactive={isFolderRead(dm)}
            onClick={() => dispatch(select({ dm }))}
          >
            {!isFolderRead(dm) ? <UnreadIcon /> : null} {dm}
          </Folder>
        ))}
      </div>
    </div>
  )

  function goToChatChannel(channel: string) {
    return function () {
      dispatch(select({ channel }))
      dispatch(openChat(channel))
    }
  }

  function goToDiscussionChannel(channel: string) {
    return function () {
      dispatch(select({ channel }))
      dispatch(openDiscussion(channel))
    }
  }
}

function Separator() {
  return <div className="separator"></div>
}

function UnreadIcon() {
  return <div className="unread-icon"></div>
}

// FIXME
function isFolderRead(c: string) {
  return true
}
