import { useDispatch, useSelector } from 'app/state'
import ChatMessageList from 'components/ChatMessageList'
import ChatItem from 'features/ChatItem'
import focusRegion from 'features/FocusRegion'
import Reminders from 'features/Reminders'
import { DateTime } from 'lib/datetime'
import React, { useEffect } from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import selectors from 'selectors'
import Input from './Input'
import {
  cleanDraft,
  cleanupDraftReminder,
  createReminderForDraft,
  editMessage,
  ensureSubscription,
  focusOnInput,
  loadMoreMessages,
  postMessage,
  selectMessage,
  setReminderForDraft,
  updateDraft,
} from './slice'

interface Props {
  channelId: string
}

export default function Chat(props: Props) {
  const dispatch = useDispatch()

  const [
    items,
    draft,
    selected,
    nextItemId,
    prevItemId,
    lastItemId,
    isLoadingMessages,
    hasMoreMessages,
    isLastMessageEditable,
    isFocusOnChat,
    isFocusOnInput,
    isFocusOnMessageList,
    isChannelInitialized,
  ] = useSelector((state) => [
    selectors.chatItems.getByChannelId(state, props.channelId),
    selectors.chat.getDraftByChannelId(state, props.channelId),
    selectors.chat.getCurrentFocusByChannelId(state, props.channelId),
    selectors.chatItems.getNextItemId(state, props.channelId),
    selectors.chatItems.getNextItemId(state, props.channelId, -1),
    selectors.chatItems.getLastItemId(state, props.channelId),
    selectors.chat.isLoadingMessages(state, props.channelId),
    selectors.chat.hasMoreMessages(state, props.channelId),
    selectors.chat.isOwnMessage(
      state,
      selectors.chatItems.getLastItemId(state, props.channelId) || ''
    ),
    selectors.focusRegion.isOnChat(state),
    selectors.chat.isFocusOnInput(state, props.channelId),
    selectors.chat.isFocusOnMessageList(state, props.channelId),
    selectors.chat.isChannelInitialized(state, props.channelId),
  ])

  useHotkeys('k, up, cmd+up', previous, { enabled: isFocusOnMessageList }, [
    selected,
  ])
  useHotkeys('j, down, cmd+down', next, { enabled: isFocusOnMessageList }, [
    selected,
  ])

  useHotkeys('j, down, cmd+down', next, { enabled: isFocusOnMessageList }, [
    selected,
  ])

  // useHotkeys('pageup', pageup, { enabled: navigationEnabled }, [selected])
  // useHotkeys('pagedown', pagedown, { enabled: navigationEnabled }, [selected])

  useHotkeys('esc', quitMessageNavigation, { enabled: isFocusOnChat })
  useHotkeys('cmd+d', toggleBetweenPanes, {
    enabled: isFocusOnChat,
    enableOnTags: ['TEXTAREA'],
  })

  useEffect(() => {
    if (!isChannelInitialized) {
      dispatch(ensureSubscription(props.channelId))
    }
  }, [props.channelId, isChannelInitialized])

  return (
    <div className="chat">
      <Reminders
        enabled={isFocusOnChat}
        focus={selected}
        createReminderForDraft={onCreateReminderForDraft}
        setReminderForDraft={onSetReminderForDraft}
        selectMessage={setMessageSelected}
        setFocusToInput={setFocusToInput}
        selectLastMessage={lastItemId ? selectLastMessage : undefined}
      >
        <ChatMessageList
          messageIds={items}
          hasMore={hasMoreMessages}
          loadMore={startLoadingMessages}
          isLoading={isLoadingMessages}
          selectedMessageId={selected.messageId}
          isFocused={isFocusOnMessageList}
          setFocusToLastMessage={selectLastMessage}
        >
          {items.map((id, ind) => (
            <ChatItem
              key={id}
              itemId={id}
              channelId={props.channelId}
              setFocusToInput={setFocusToInput}
              selected={
                isFocusOnMessageList && id === selected.messageId
                  ? selected
                  : undefined
              }
              previousItemId={items[ind - 1]}
              nextItemId={items[ind + 1]}
              onClick={() => setMessageSelected(id)}
            />
          ))}
        </ChatMessageList>
        <Input
          enabled={isFocusOnInput}
          draft={draft}
          clearDraft={clearDraftValue}
          postDraftMessage={postDraftMessage}
          setDraftValue={setDraftValue}
          removeDraftReminder={removeDraftReminder}
          editLastMessage={editLastMessage}
          setFocusToInput={setFocusToInput}
          selectLastMessage={selectLastMessage}
          placeholder={`Message ${props.channelId}`}
        />
      </Reminders>
    </div>
  )

  function startLoadingMessages() {
    if (isChannelInitialized) {
      dispatch(loadMoreMessages(props.channelId))
    }
  }

  function previous(e: KeyboardEvent) {
    e.preventDefault()

    if (prevItemId) {
      dispatch(selectMessage({ channelId: props.channelId, id: prevItemId }))
    }
  }

  function next(e: KeyboardEvent) {
    e.preventDefault()

    if (nextItemId) {
      dispatch(selectMessage({ channelId: props.channelId, id: nextItemId }))
    } else {
      dispatch(focusOnInput({ channelId: props.channelId }))
    }
  }

  function quitMessageNavigation(e: KeyboardEvent) {
    e.preventDefault()
    dispatch(focusOnInput({ channelId: props.channelId }))
  }

  function editLastMessage() {
    if (isLastMessageEditable && lastItemId) {
      dispatch(
        editMessage({ channelId: props.channelId, messageId: lastItemId })
      )
    }
  }

  function removeDraftReminder() {
    if (draft.remindAt) {
      dispatch(cleanupDraftReminder({ channelId: props.channelId }))
    }
  }

  function onCreateReminderForDraft(remindAt: DateTime) {
    dispatch(
      createReminderForDraft({
        channelId: props.channelId,
        remindAt,
      })
    )
  }

  function onSetReminderForDraft() {
    dispatch(
      setReminderForDraft({
        channelId: props.channelId,
      })
    )
  }

  function clearDraftValue() {
    dispatch(cleanDraft({ channelId: props.channelId }))
  }

  function postDraftMessage() {
    dispatch(postMessage(draft))
  }

  function setDraftValue(value: string) {
    dispatch(updateDraft({ channelId: props.channelId, body: value }))
  }

  function setFocusToInput() {
    dispatch(focusOnInput({ channelId: props.channelId }))
    dispatch(focusRegion.set(focusRegion.Region.Chat))
  }

  function selectLastMessage() {
    if (lastItemId) {
      setMessageSelected(lastItemId)
    }
  }

  function setMessageSelected(id: string) {
    dispatch(selectMessage({ channelId: props.channelId, id }))
    dispatch(focusRegion.set(focusRegion.Region.Chat))
  }

  function toggleBetweenPanes(e: KeyboardEvent) {
    e.preventDefault()
    dispatch(focusRegion.toggle())
  }
}
