import { RootState } from 'app/state'
import { ChatMessage, Reminder } from 'app/state/entities'
import datetime, { DateTime } from 'lib/datetime'
import selectors from 'selectors'

export interface ChatItem {
  id: string
  displayDate: DateTime
  chatMessage?: ChatMessage
  reminder?: Reminder
}

export function getById(state: RootState, id: string): ChatItem | undefined {
  return getByMessageId(state, id) || getByReminderId(state, id)
}

export function getByChannelId(state: RootState, channelId: string): string[] {
  const chatMessages = selectors.chat.getMessagesByChannelId(state, channelId)
  const returnedReminders = selectors.reminders.getReturnedRemindersByChannelId(
    state,
    channelId
  )

  const items = <ChatItem[]>chatMessages
    .map((id: string) => getByMessageId(state, id))
    .concat(returnedReminders.map((id: string) => getByReminderId(state, id)))
    .filter((item) => !!item)

  return items.sort(sortItemsByDate).map((item) => item.id)
}

export function getByMessageId(
  state: RootState,
  messageId: string
): ChatItem | undefined {
  const record = selectors.chatMessages.getById(state, messageId)
  if (!record) return

  return {
    id: messageId,
    displayDate: record.sentAt,
    chatMessage: record,
  }
}

export function getByReminderId(
  state: RootState,
  reminderId: string
): ChatItem | undefined {
  const record = selectors.reminders.getById(state, reminderId)
  if (!record) return

  return {
    id: reminderId,
    displayDate: record.remindAt,
    reminder: record,
    chatMessage: selectors.chatMessages.getById(state, record.chatMessageId),
  }
}

function sortItemsByDate(a: ChatItem, b: ChatItem): number {
  if (datetime.isBefore(a.displayDate, b.displayDate)) return -1
  if (datetime.isAfter(a.displayDate, b.displayDate)) return 1
  return 0
}

export function getNextItemId(
  state: RootState,
  channelId: string,
  jumpBy?: number
) {
  const items = getByChannelId(state, channelId)
  const currentIndex = findCurrentItemIndex(state, channelId)
  const nextIndex = currentIndex + (jumpBy === undefined ? 1 : jumpBy)

  if (currentIndex === -1) {
    return items[items.length - 1]
  }

  if (nextIndex >= items.length || nextIndex < 0) {
    return
  }

  return items[nextIndex]
}

export function findCurrentItemIndex(
  state: RootState,
  channelId: string
): number {
  const selectedMessageId = selectors.chat.getCurrentFocusByChannelId(
    state,
    channelId
  ).messageId

  if (!selectedMessageId) return -1

  const items = getByChannelId(state, channelId)
  return items.indexOf(selectedMessageId)
}

export function getLastItemId(
  state: RootState,
  channelId: string
): string | undefined {
  return getByChannelId(state, channelId).slice(-1)[0]
}
