import React from 'react'

const Folder = (props: {
  children: React.ReactNode
  inactive?: boolean
  channel?: boolean
  selected?: boolean
  onClick?: () => void
}) => {
  return (
    <div
      onClick={props.onClick}
      className={`folder ${props.inactive ? 'inactive' : ''} ${
        props.selected ? 'selected' : ''
      }`}
    >
      {props.children}
    </div>
  )
}

export default Folder
