import { useDispatch, useSelector } from 'app/state'
import React, { useState } from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import selectors from 'selectors'
import { menu as cmdkMenu } from './command-k'
import './index.css'
import { setAsOpen } from './slice'
import {
  Command,
  CommandPaletteContext,
  CommandRelevancyResult,
  Context,
  DialogOptions,
  initialOptions,
} from './use-command-palette'

export {
  CommandType,
  default as useCommandPalette,
  match,
  mustMatch,
} from './use-command-palette'
export type { Command, CommandRelevancyResult } from './use-command-palette'

interface Props {
  children: React.ReactNode
}

export default function CommandPalette(props: Props) {
  const dispatch = useDispatch()

  const [isOpen] = useSelector((state) => [
    selectors.commandPalette.isOpen(state),
  ])

  const [options, setOptions] = useState<CommandPaletteContext>({
    ...initialOptions,
    open,
    register,
    deregister,
  })

  const [input, setInput] = useState('')
  const [selectedIndex, setSelectedIndex] = useState(0)

  useHotkeys('cmd+k', onPressCmdK, { enableOnTags: ['INPUT', 'TEXTAREA'] }, [
    options,
  ])

  useHotkeys('up', previous, {
    enableOnTags: ['INPUT'],
    enabled: isOpen,
  })
  useHotkeys('down', next, { enableOnTags: ['INPUT'], enabled: isOpen })
  useHotkeys('enter', select, {
    enableOnTags: ['INPUT'],
    enabled: isOpen,
    keydown: true,
  })
  useHotkeys('esc', close, {
    enableOnTags: ['INPUT'],
    enabled: isOpen,
    keydown: true,
  })

  return (
    <Context.Provider value={options}>
      {props.children}
      {isOpen ? (
        <div className="overlay">
          <div className="commandbar">
            <div className="heading">
              <label></label>
              {options.heading}
            </div>
            <div className="input">
              <input
                placeholder={options.placeholder}
                value={input}
                onInput={onInput}
                autoFocus
              />
            </div>
            <div className="commands">
              {options.menu
                .filter((cmd) => filter(cmd.title))
                .map((cmd, ind) => (
                  <div
                    key={ind}
                    className={`row ${ind === selectedIndex ? 'selected' : ''}`}
                  >
                    <div className="title">{cmd.title}</div>
                  </div>
                ))}
            </div>
          </div>
        </div>
      ) : null}
    </Context.Provider>
  )

  function select(e: KeyboardEvent) {
    const selected = options.menu[selectedIndex]

    e.preventDefault()
    dispatch(setAsOpen(false))
    reset()

    if (options.callback) options.callback(selected)
  }

  function close(e: KeyboardEvent) {
    e.preventDefault()
    dispatch(setAsOpen(false))
    reset()
    if (options.callback) options.callback()
  }

  function previous(e: KeyboardEvent) {
    if (selectedIndex === 0) {
      return setSelectedIndex(options.menu.length - 1)
    }

    setSelectedIndex(selectedIndex - 1)
  }

  function next() {
    if (selectedIndex === options.menu.length - 1) {
      return setSelectedIndex(0)
    }

    setSelectedIndex(selectedIndex + 1)
  }

  function onInput(e: React.KeyboardEvent<HTMLInputElement>) {
    setInput(e.currentTarget.value)
  }

  function filter(title: string): boolean {
    return slug(title).includes(slug(input))
  }

  function slug(input: string): string {
    return input.toLowerCase().replace(/[^a-z0-9]/g, '')
  }

  function reset() {
    setSelectedIndex(0)
    setInput('')
    setOptions((currentOptions) => ({
      ...currentOptions,
      heading: '',
      menu: [],
    }))
  }

  function register(
    title: string,
    fn: () => void,
    relevancy?: CommandRelevancyResult[]
  ) {
    const key = slug(title)

    setOptions((currentOptions) => ({
      ...currentOptions,
      registry: {
        ...currentOptions.registry,
        [key]: { key, title, fn, relevancy, value: title } as Command,
      },
    }))
  }

  function deregister(...commandsToDelete: string[]) {
    setOptions((currentOptions) => {
      const copy = { ...currentOptions.registry }

      for (let cmdName of commandsToDelete) {
        delete copy[slug(cmdName)]
      }

      return {
        ...currentOptions,
        registry: copy,
      }
    })
  }

  function open(
    dialogOptions: DialogOptions,
    callback: (command?: Command) => void
  ) {
    setOptions((currentOptions) => ({
      ...currentOptions,
      callback,
      ...dialogOptions,
    }))

    dispatch(setAsOpen(true))
  }

  function onPressCmdK() {
    setOptions((currentOptions) => {
      return {
        ...currentOptions,
        heading: 'Superhuman Command',
        menu: cmdkMenu(Object.values(options.registry)),
        callback: (cmd: Command | undefined) => {
          if (cmd?.fn) {
            cmd.fn()
          }
        },
      }
    })

    dispatch(setAsOpen(true))
  }
}
