import { useDispatch, useSelector } from 'app/state'
import ChatThread from 'features/ChatThread'
import React from 'react'
import selectors from 'selectors'

interface Props {
  channelId: string
}

export default function ChannelSidebar(props: Props) {
  const dispatch = useDispatch()

  const [threadId] = useSelector((state) => [
    selectors.channels.getCurrentChatThreadId(state, props.channelId),
  ])

  return (
    <div className="channel-sidebar">
      {threadId ? (
        <ChatThread channelId={props.channelId} threadId={threadId} />
      ) : null}
    </div>
  )
}
