import { RootState } from 'app/state'

export function getCurrentChatThreadId(
  state: RootState,
  channelId: string
): string | undefined {
  return state.channel[channelId]?.chatThreadId
}

export function isFocusOnChatTab(state: RootState, id: string): boolean {
  return !state.channel[id]?.viewingDiscussion
}

export function isFocusOnDiscussionTab(state: RootState, id: string): boolean {
  return state.channel[id]?.viewingDiscussion
}
