import { configureStore } from '@reduxjs/toolkit'
import logger from './logger'
import { store as persistentStore } from './persistency'
import allReducers from './reducers'

// Basic store with persistency disabled
export const basicStore = configureStore({
  reducer: allReducers,
  middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(logger),
})

export const store = process.env.REACT_APP_ENABLE_PERSISTENCY
  ? persistentStore
  : basicStore

export type RootState = ReturnType<typeof basicStore.getState>
export type AppDispatch = typeof basicStore.dispatch

if (process.env.REACT_APP_ENABLE_PERSISTENCY) {
  console.log('Persistency is enabled.')
}
