import { RootState } from 'app/state'
import { Reminder, Reminders } from 'app/state/entities'
import datetime from 'lib/datetime'
import selectors from 'selectors'

export function getById(state: RootState, id: string): Reminder | undefined {
  const record = selectors.entities.getRecordById(state, Reminders, id)
  if (record) return <Reminder>record
}

export function all(state: RootState): Reminder[] {
  return <Reminder[]>(
    Object.values(selectors.entities.getAllRecords(state, Reminders))
  )
}

export function getByMessageId(
  state: RootState,
  messageId: string
): Reminder[] {
  return all(state).filter((r) => r.chatMessageId === messageId)
}

export function getLatestNotReturnedByMessageId(
  state: RootState,
  messageId: string
): Reminder | undefined {
  return getByMessageId(state, messageId)
    .filter((r) => datetime.isAfter(r.remindAt, datetime.now()))
    .sort((a, b) => a.remindAt.seconds - b.remindAt.seconds)[0]
}

export function getReturnedReminders(state: RootState): string[] {
  return all(state)
    .filter((r) => datetime.isBefore(r.remindAt, datetime.now()) && !r.archived)
    .map((r) => r.id)
}

export function getReturnedRemindersByChannelId(
  state: RootState,
  channelId: string
): string[] {
  return all(state)
    .filter(
      (r) =>
        selectors.chatMessages.getById(state, r.chatMessageId)?.channelId ===
          channelId &&
        datetime.isBefore(r.remindAt, datetime.now()) &&
        !r.archived
    )
    .map((r) => r.id)
}
