import React from 'react'

interface Props {
  width: number
  height?: number
  block?: boolean
}

export default function Placeholder(props: Props) {
  const css = {
    display: props.block ? 'block' : 'inline-block',
    width: `${props.width * 50}px`,
    height: props.height ? `${props.height * 50}px` : '',
    background: '#ddd',
    borderRadius: '2px',
    margin: props.block ? '0 0 5px 0' : '',
  }

  return <label style={css}>&nbsp;</label>
}
