import { RootState } from 'app/state'
import { Entity, Table } from 'app/state/entities'

export function getRecordById(
  state: RootState,
  table: Table,
  id: string
): Entity | undefined {
  return getAllRecords(state, table)[id]
}

export function getAllRecords(
  state: RootState,
  table: Table
): Record<string, Entity> {
  return state.entities[table] || {}
}
